<template>
  <div id="task-timing">
    <div>
      <div id="task-info">
        <TaskType
          v-model="state.taskType"
          :readonly="true"
        />
        <span>{{ state.name }}</span>
        <span v-if="state.expectedTime!==0">预期时间</span>
        <span v-if="state.expectedTime!==0">{{ state.expectedTime }}</span>
        <span v-if="state.expectedTime!==0">分钟</span>
      </div>
      <div id="sub-tasks">
        <div
          v-for="(item, index) in state.subTasks"
          :key="index"
          class="sub-task"
          :class="[currentSubTaskIndex===index?'selected':'']"
        >
          <i
            class="el-icon-right"
            @click="shift(item, index)"
          />
          <span style="width: 12em;">{{ item.name }}</span>
          <span>已进行</span>
          <span>{{ getMinute(item.time) }}</span>
          <span>分钟</span>
          <span v-if="item.expectedTime!==0">预期时间</span>
          <span v-if="item.expectedTime!==0">{{ item.expectedTime }}</span>
          <span v-if="item.expectedTime!==0">分钟</span>
        </div>
      </div>
      <el-input
        v-model="state.note"
        type="textarea"
        :rows="20"
        placeholder="笔记"
        style="width: 80%; margin-top: auto;"
      />
    </div>
    <div>
      <ProcessCircle
        :current-task-index="currentSubTaskIndex"
        :tasks="tasks"
        :time="timeSegment"
        :percentage="percentage"
        :working="isWorking"
        ref="processEl"
      />
      <div class="button-group">
        <el-button
          v-if="!isWorking"
          @click="start"
        >
          开始
        </el-button>
        <el-button
          v-else
          @click="pause"
        >
          暂停
        </el-button>
        <el-button
          @click="finish"
        >
          完成
        </el-button>
      </div>
    </div>
    <div>
      <Track :data="state.track" />
    </div>
  </div>
</template>

<script>
import { onBeforeRouteLeave, useRouter, useRoute } from 'vue-router'
import { onActivated, ref } from 'vue'
import { useStore } from 'vuex'
import TaskType from '@/components/TaskType'
import Track from '@/components/Track'
import TimeManager from '@/utils/time'
import ProcessCircle from './ProcessCircle'
import { ElMessage } from 'element-plus'

export default {
  name: 'Timing',
  components: {
    TaskType,
    Track,
    ProcessCircle
  },
  setup () {
    const router = useRouter()

    const state = useStore().state.task.currentTask

    const timeSegment = ref(0)
    const percentage = ref(0)
    const isWorking = ref(false)
    const currentTask = ref({})
    const currentSubTaskIndex = ref(0)
    const tasks = ref([])
    let timer = 0
    let startTime = null
    let endTime = null

    const processEl = ref(null)

    onActivated(() => {
      if (useRoute().params.from === 'Start') {
        reTiming()
        // 可能是因为子组件还没有接受到变化
        setTimeout(() => {
          processEl.value.draw()
        }, 0)
      }
    })

    const reTiming = () => {
      clearTime()
      state.note = ''
      state.track.splice(0)
      timeSegment.value = 0
      percentage.value = 0
      startTime = null
      endTime = null
      setCurrentTask()
      setTasks()
      currentSubTaskIndex.value = 0
    }

    const clearTime = () => {
      state.time = 0
      state.subTasks.forEach(item => {
        item.time = 0
      })
    }

    const setCurrentTask = () => {
      if (state.subTasks.length === 0) {
        currentTask.value = { name: state.name, time: 0, expectedTime: state.expectedTime }
      } else {
        currentTask.value = state.subTasks[0]
      }
    }

    const setTasks = () => {
      if (state.subTasks.length === 0) {
        tasks.value = [{ name: state.name, time: 0, expectedTime: state.expectedTime }]
      } else {
        tasks.value = state.subTasks
      }
    }

    setCurrentTask()
    setTasks()

    const getMinute = TimeManager.second2Minute

    const shift = (item, index) => {
      if (isWorking.value) {
        pause()
        currentSubTaskIndex.value = index
        currentTask.value = item
        start()
      } else {
        currentSubTaskIndex.value = index
        currentTask.value = item
      }
    }

    const start = () => {
      clearInterval(timer) // 结束休息计时
      endTime = new Date() // 设置休息结束时间
      addTrack('休息')
      startTime = new Date() // 设置任务开始时间
      timeSegment.value = 0
      isWorking.value = true

      let processIncrement = 0
      if (currentTask.value.expectedTime === 0) {
        percentage.value = 100
      } else {
        percentage.value = currentTask.value.time / (currentTask.value.expectedTime * 60) * 100
        processIncrement = 100 / currentTask.value.expectedTime / 60
        if (percentage.value >= 100) {
          percentage.value = 100
          processIncrement = 0
        }
      }

      timer = setInterval(() => {
        timeSegment.value++
        currentTask.value.time += 1
        percentage.value += processIncrement
        if (percentage.value >= 100) {
          percentage.value = 100
        }
      }, 1000)
    }

    const pause = () => {
      clearInterval(timer) // 结束任务计时
      endTime = new Date() // 设置任务结束时间
      addTrack()
      startTime = new Date() // 设置休息开始时间
      timeSegment.value = 0

      isWorking.value = false

      timer = setInterval(() => {
        timeSegment.value++
      }, 1000)
    }

    // 有两种情形会addTrack
    // 1. 任务暂停时
    // 2. 任务开始时
    const addTrack = (name = currentTask.value.name) => {
      // 第一次开始不计入
      if (startTime === null) return
      // 休息时间小于1分钟不计入
      if (!isWorking.value && timeSegment.value < 60) return
      state.track.push({
        name: name,
        time: getMinute(timeSegment.value),
        startTime: startTime,
        endTime: endTime
      })
    }

    const finish = () => {
      clearInterval(timer)

      if (isWorking.value) {
        endTime = new Date()
        addTrack()
        isWorking.value = false
      }

      state.time = 0
      state.track.forEach(item => {
        if (item.name !== '休息') {
          state.time += item.time
        }
      })

      if (state.track.length === 0) {
        ElMessage({
          showClose: true,
          message: '未进行任何任务',
          type: 'error'
        })
        return
      }

      if (state.subTasks.length === 0) {
        state.startTime = startTime
        state.endTime = endTime
      } else {
        state.startTime = state.track[0].startTime
        state.endTime = state.track[state.track.length - 1].endTime
      }

      router.push({
        name: 'Finish'
      })
    }

    onBeforeRouteLeave((to, from) => {
      if (isWorking.value) {
        pause()
      }
      timeSegment.value = 0
      clearInterval(timer)
    })

    return {
      state,
      percentage,
      timeSegment,
      isWorking,
      start,
      pause,
      finish,
      shift,
      getMinute,
      currentTask,
      currentSubTaskIndex,
      processEl,
      tasks
    }
  }
}
</script>

<style lang="less" scoped>

#task-timing {
  display: flex;
  justify-content: space-between;
  width: 100%;

  >div{
    width: 33%;

    &:nth-child(2){
      margin-top: 5em;

      .button-group{
        display: flex;
        justify-content: center;

        >:nth-child(2){
          margin-left: 5em;
        }
      }
    }

    &:nth-child(3){
      overflow: auto;
      >*{
        margin-left: 5em;
      }
    }
  }

  >:first-child{
    display: flex;
    flex-direction: column;
  }

  #task-info{
    display: flex;
    flex-direction: row;
    margin: 1em 0;
    align-items: center;

    >:nth-child(1){
      margin-right: 1em;
    }
    >:nth-child(2){
      margin-right: 1em;
    }
    >:nth-child(4){
      margin: 0 0.3em;
    }
  }

  .sub-task{
    display: flex;
    align-items: center;
    transition: 200ms;
    margin: 1em 0;
    font-weight: bold;

    i{
      cursor: pointer;
      line-height: unset;
    }

    >:nth-child(2){
      margin: 0 1em;
    }

    >:nth-child(4){
      margin: 0 0.3em;
    }

    >:nth-child(5){
      margin-right: 1em;
    }

    >:nth-child(7){
      margin: 0 0.3em;
    }
  }
}

.selected{
  color: #409EFF;
}
</style>
