<template>
  <div id="task-template">
    <h1>模板</h1>
    <el-divider />

    <div class="title">
      <h3>常用</h3>
      <AddButton :type="0" />
    </div>
    <div
      id="common"
      class="card-container"
    >
      <Item
        v-for="item in common"
        :key="item.id"
        :data="item"
        :type="0"
      />
    </div>
    <el-divider />

    <div class="title">
      <h3>自定义</h3>
      <AddButton :type="1" />
    </div>
    <div
      id="custom"
      class="card-container"
    >
      <Item
        v-for="item in custom"
        :key="item.id"
        :data="item"
        :type="1"
      />
    </div>

    <DetailPopup />
  </div>
</template>

<script>
import { useStore } from 'vuex'
import { getTaskTypeFullName } from '@/utils/taskType'
import Item from './Item'
import AddButton from './AddButton'
import DetailPopup from './DetailPopup'

export default {
  name: 'TaskTemplate',
  components: {
    Item,
    AddButton,
    DetailPopup
  },
  setup () {
    const state = useStore().state.task
    useStore().dispatch('task/init')

    const common = state.common
    const custom = state.custom

    return {
      common,
      custom,
      getTaskTypeFullName
    }
  }
}
</script>

<style lang="less" scoped>
@import "/src/assets/css/text.less";

#task-template {

  .title{
    display: flex;
    flex-direction: row;
    align-content: center;
    padding-bottom: 0.5em;

    h3{
      margin-right: auto;
    }
  }

  .card-container{
    display: flex;
    flex-wrap: wrap;
    transition: 200ms;

    >*{
      margin-right: 9.7px;
    }

    >:nth-child(4n){
      margin-right: 0;
    }
  }
}
</style>
