<template>
  <el-dialog
    title="修改时间限制计划"
    v-model="show"
    width="35%"
  >
    <el-form
      :model="data"
      :rules="rules"
    >
      <el-form-item
        label="名称"
        prop="name"
      >
        <el-input
          v-model="data.name"
          style="width: 20em;"
          size="medium"
          clearable
        />
      </el-form-item>
      <el-form-item
        label="计划类型"
        prop="taskType"
      >
        <TaskType
          v-model="data.taskType"
          size="medium"
        />
      </el-form-item>
      <el-form-item
        label="开始时间"
        prop="startTime"
      >
        <el-time-picker
          v-model="data.startTime[0]"
          @change="onStartTimeChange"
          format="HH:mm"
          placeholder="无"
          size="medium"
        />
        <el-time-picker
          v-model="data.startTime[1]"
          @change="onStartTimeChange"
          format="HH:mm"
          placeholder="无"
          size="medium"
        />
        <span>{{ showTimeOrder(startTimeOrder, '开始') }}</span>
      </el-form-item>
      <el-form-item
        label="结束时间"
        prop="endTime"
      >
        <el-time-picker
          v-model="data.endTime[0]"
          @change="onEndTimeChange"
          format="HH:mm"
          placeholder="无"
          size="medium"
        />
        <el-time-picker
          v-model="data.endTime[1]"
          @change="onEndTimeChange"
          format="HH:mm"
          placeholder="无"
          size="medium"
        />
        <span>{{ showTimeOrder(endTimeOrder, '完成') }}</span>
      </el-form-item>
      <el-form-item
        label="持续时间"
        prop="time"
      >
        <el-input-number
          v-model="data.time"
          :min="0"
          :step="5"
          size="medium"
          class="input-expected-time"
          :disabled="data.subTasks.length!==0"
        />
        <span>分钟</span>
      </el-form-item>
      <el-form-item
        label="子任务"
        prop="subTasks"
      >
        <SubTasks
          :data="data.subTasks"
          v-model:expected-time="data.time"
        />
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button
          type="primary"
          @click="modify"
        >确 定</el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import { ref, watch } from 'vue'
import { VModel } from '@/utils'
import { planHandleProxy } from '../TimeView/planProxy'
import TaskType from '@/components/TaskType'
import SubTasks from '@/components/SubTasks'
import planDetails from '@/store/planDetails'
import dayjs from 'dayjs'
import _ from 'lodash'

export default {
  name: 'UpdateTimeLimitedPlanPopup',
  components: {
    TaskType,
    SubTasks
  },
  props: {
    modelValue: {
      type: Boolean,
      default: false
    },
    date: {
      type: Object,
      default: () => new Date()
    }
  },
  emits: ['update:modelValue'],
  setup (props, ctx) {
    const show = VModel(props, ctx)

    const data = ref({})

    watch(show, () => {
      if (show) {
        data.value = _.cloneDeep(planDetails.get())
        onStartTimeChange()
        onEndTimeChange()
      }
    })

    const startTimeOrder = ref(0)
    const endTimeOrder = ref(0)

    watch(() => props.date, () => {
      data.value.startTime = [props.date, props.date]
      data.value.endTime = [props.date, props.date]
    })

    const onStartTimeChange = () => {
      setTimeOrder(data.value.startTime, startTimeOrder)
    }

    const onEndTimeChange = () => {
      setTimeOrder(data.value.endTime, endTimeOrder)
    }

    const setTimeOrder = (time, timeOrder) => {
      if (time[0] !== null) {
        if (time[1] !== null) {
          timeOrder.value = 3
        } else {
          timeOrder.value = 2
        }
      } else {
        if (time[1] !== null) {
          timeOrder.value = 1
        } else {
          timeOrder.value = 0
        }
      }
    }

    const showTimeOrder = (order, postfix) => {
      switch (order) {
        case 1:
          return '之前' + postfix
        case 2:
          return '之后' + postfix
        case 3:
          return '之间' + postfix
        default:
          return ''
      }
    }

    const modify = () => {
      planHandleProxy.updateTimeLimitedPlan({
        id: data.value.id,
        name: data.value.name,
        taskType: data.value.taskType,
        startTime: data.value.startTime,
        endTime: data.value.endTime,
        time: data.value.time,
        subTasks: data.value.subTasks
      })
      planDetails.set(data.value)
      data.value = null
      show.value = false
    }

    const cancel = () => {
      data.value = null
      show.value = false
    }

    const validateTime = (rule, value, cb) => {
      if (value[0] && value[1]) {
        if (dayjs(value[0]).isBefore(dayjs(value[1]))) {
          cb()
        } else {
          cb(new Error('sb'))
        }
      } else {
        cb()
      }
    }

    const validateEndTime = (rule, value, cb) => {
      if (value[0] || value[1]) {
        if (!data.value.startTime[0] && !data.value.startTime[1]) {
          cb()
        } else {
          const startTime = data.value.startTime[1] ? data.value.startTime[1] : data.value.startTime[0]
          const endTime = value[0] ? value[0] : value[1]
          if (!dayjs(endTime).isBefore(dayjs(startTime))) {
            cb()
          } else {
            cb(new Error('dsb'))
          }
        }
      }
    }

    const rules = {
      startTime: [
        { validator: validateTime, trigger: 'blur' }
      ],
      endTime: [
        { validator: validateTime, trigger: 'blur' },
        { validator: validateEndTime, trigger: 'blur' }
      ],
      name: [
        { required: true, trigger: 'change', message: 'sb' }
      ],
      taskType: [
        { required: true, trigger: 'change', message: 'sb' }
      ]
    }

    return {
      data,
      show,
      startTimeOrder,
      endTimeOrder,
      showTimeOrder,
      onStartTimeChange,
      onEndTimeChange,
      modify,
      cancel,
      rules
    }
  }
}
</script>

<style lang="less" scoped>

.el-form{

  .el-form-item{
    display: flex;

    /deep/label{
      width: 7em;
    }

    /deep/.el-form-item__content{
      >*{
        margin-right: 0.5em;
      }
    }
  }
}

/deep/ .el-date-editor.el-input{
  width: 8em;
}

.input-expected-time{
  width: 9em;
}
</style>
