import TimeManager from '@/utils/time'
import dayjs from 'dayjs'
import { isVagueMatch } from '@/utils/taskType'

const taskProxy = {
  orderRule: null,
  timeRule: null,
  frequencyRule: null,
  task: null,
  init (store) {
    this.store = store
    this.orderRule = store.state.orderRule.data
    this.timeRule = store.state.timeRule.data
    this.frequencyRule = store.state.frequencyRule.data
    this.state = store.state.statistics
  },
  invoke (task) {
    this.task = task
    const time = this.validateTimeRule()
    if (typeof time === 'object') {
      return {
        rule: 'time',
        taskType: task.taskType,
        ...time
      }
    }
    const frequency = this.validateFrequencyRule()
    if (typeof frequency === 'object') {
      return {
        rule: 'frequency',
        taskType: task.taskType,
        frequency: frequency.frequency
      }
    }
    const order = this.validateOrderRule()
    if (typeof order === 'object') {
      return {
        rule: 'order',
        taskType: task.taskType,
        order: order.order,
        basedTaskType: order.basedTaskType
      }
    }
    return true
  },
  validateTimeRule () {
    for (const rule of this.timeRule) {
      if (!this.validateTaskType(rule.taskType)) {
        continue
      }
      const startTime = TimeManager.string2time(rule.startTime)
      const endTime = TimeManager.string2time(rule.endTime)
      if (TimeManager.in(new Date(), startTime, endTime)) {
        return { startTime, endTime }
      }
    }
    return true
  },
  validateFrequencyRule () {
    for (const rule of this.frequencyRule) {
      if (!this.validateTaskType(rule.taskType)) {
        continue
      }
      const tasks = []
      switch (rule.time) {
        case 0: // 日
          this.state.allData.forEach(task => {
            if (task.startTime.year() === task.endTime.year() &&
              task.startTime.month() === task.endTime.month() &&
              task.startTime.date() === task.endTime.date() &&
              task.startTime.year() === dayjs().year() &&
              task.startTime.month() === dayjs().month() &&
              task.startTime.date() === dayjs().date() &&
              isVagueMatch(task.taskType, rule.taskType)) {
              tasks.push(task)
            }
          })
          break
        case 1: // 周
          this.state.allData.forEach(task => {
            if (dayjs().subtract(7, 'date').isBefore(task.startTime, 'date')
            ) { tasks.push(task) }
          })
          break
        case 2: // 月
          this.state.allData.forEach(task => {
            if (dayjs().subtract(1, 'month').isBefore(task.startTime, 'date')
            ) { tasks.push(task) }
          })
          break
      }
      if (tasks.length >= rule.max) {
        return { frequency: rule.max }
      }
      return true
    }
  },
  validateOrderRule () {
    for (const rule of this.orderRule) {
      if (!this.validateTaskType(rule.bannedTaskType)) {
        continue
      }
      if (rule.order === 0) {
        console.log(rule, this.task, this.state.allData)
        let flag = false
        for (const task of this.state.allData) {
          if (isVagueMatch(task.taskType, rule.basedTaskType)) {
            flag = true
          }
        }
        if (!flag) {
          return { basedTaskType: rule.basedTaskType, order: rule.order }
        }
      } else {
        console.log(rule, this.task, this.state.allData)
        for (const task of this.state.allData) {
          if (isVagueMatch(task.taskType, rule.basedTaskType)) {
            return { basedTaskType: rule.basedTaskType, order: rule.order }
          }
        }
      }
    }
  },
  validateTaskType (taskType) {
    return isVagueMatch(this.task.taskType, taskType)
  }
}

export default taskProxy
