<template>
  <div id="filter">
    <div>
      <div
        v-if="state.isTemplate"
        style="display: flex;justify-content: center;"
      >
        <el-divider>
          <el-input
            v-model="state.currentFilter.name"
            size="medium"
            style="width: 15em;"
            clearable
          />
        </el-divider>
      </div>
      <TemplateGroup
        title="时间"
        name="Time"
        :data="state.currentFilter.times"
      />
      <TemplateGroup
        title="名称"
        name="Name"
        :data="state.currentFilter.names"
      />
      <TemplateGroup
        title="类型"
        name="Type"
        :data="state.currentFilter.types"
      />
      <TemplateGroup
        title="投入度"
        name="Rate"
        :data="state.currentFilter.devotions"
      />
      <TemplateGroup
        title="满意度"
        name="Rate"
        :data="state.currentFilter.satisfactions"
      />
    </div>
    <div id="button-group">
      <div v-if="state.isTemplate">
        <el-button
          @click="close"
          type="danger"
          plain
          size="mini"
        >
          关闭
        </el-button>
        <el-button
          @click="remove"
          type="danger"
          plain
          size="mini"
        >
          删除
        </el-button>
        <el-button
          @click="update"
          type="primary"
          plain
          size="mini"
        >
          更新
        </el-button>
        <el-button
          @click="apply"
          type="primary"
          plain
          size="mini"
        >
          应用
        </el-button>
      </div>
      <div v-else>
        <el-button
          @click="inputNamePopup=true"
          type="primary"
          plain
          size="mini"
        >
          保存
        </el-button>
        <el-button
          @click="apply"
          type="primary"
          size="mini"
        >
          应用
        </el-button>
      </div>
    </div>
  </div>
  <el-dialog
    title="提示"
    v-model="inputNamePopup"
    width="100%"
    fullscreen
    :modal="false"
  >
    <div>模板名称：</div>
    <el-input v-model="name" />
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="inputNamePopup = false">
          取 消
        </el-button>
        <el-button
          type="primary"
          @click="addFilterTemplate"
        >
          确 定
        </el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import { ref, watch } from 'vue'
import { useStore } from 'vuex'
import TemplateGroup from './TemplateGroup'
import _ from 'lodash'
import { ElMessage } from 'element-plus'

export default {
  name: 'Filter',
  components: {
    TemplateGroup
  },
  setup () {
    const store = useStore()
    const state = store.state.statistics

    const mountTemplate = () => {
      state.currentFilter = _.cloneDeep(state.currentTemplate)
    }

    const unmountTemplate = () => {
      state.currentTemplate = null
      state.currentFilter = {
        name: '',
        names: [{ name: '' }],
        types: [{ isNegate: false, type: null }],
        devotions: [{ operator: null, rate: null }],
        satisfactions: [{ operator: null, rate: null }],
        times: [{ repeatType: 0, date: null, isNegate: false }]
      }
    }

    watch(() => state.isTemplate, () => {
      console.log(state.isTemplate)
      if (state.isTemplate) {
        mountTemplate()
      } else {
        unmountTemplate()
      }
    })

    watch(() => state.currentTemplate, () => {
      if (state.currentTemplate) { // 选择了过滤模板
        mountTemplate()
      }
    })

    const apply = () => {
      if (_.isEqual(state.currentFilter, state.currentTemplate)) {
        store.dispatch('statistics/getDataWithId', state.currentFilter.id)
          .then(_ => {
            ElMessage({
              message: '应用成功！',
              type: 'success',
              showClose: true
            })
          })
      } else {
        store.dispatch('statistics/getDataWithoutId', state.currentFilter)
          .then(_ => {
            ElMessage({
              message: '应用成功！',
              type: 'success',
              showClose: true
            })
          })
      }
    }

    const close = () => {
      state.isTemplate = false
    }

    const update = () => {
      store.dispatch('statistics/updateFilter', state.currentFilter)
        .then(_ => {
          ElMessage({
            message: '更新成功！',
            type: 'success',
            showClose: true
          })
        })
    }

    const remove = () => {
      store.dispatch('statistics/removeFilter', state.currentFilter.id)
        .then(_ => {
          state.isTemplate = false
          ElMessage({
            message: '删除成功！',
            type: 'success',
            showClose: true
          })
        })
    }

    const name = ref('')
    const inputNamePopup = ref(false)

    const addFilterTemplate = () => {
      store.dispatch('statistics/addFilter', {
        ...state.currentFilter,
        name: name.value
      }).then(_ => {
        ElMessage({
          message: '添加成功！',
          type: 'success',
          showClose: true
        })
      })
      inputNamePopup.value = false
    }

    return {
      state,
      remove,
      close,
      update,
      name,
      apply,
      inputNamePopup,
      addFilterTemplate
    }
  }
}
</script>

<style lang="less" scoped>
#filter{
  >:nth-child(1){
    display: flex;
    flex-direction: column;

    >*{
      margin-bottom: 1em;
    }
  }
}

#button-group{
  display: flex;
   >:first-child{
     margin-left: auto;
   }
}
</style>
