<template>
  <el-card id="frequency-rule">
    <template #header>
      <div id="title">
        <h1>次数限制</h1>
      </div>
    </template>
    <div id="content">
      <Item
        v-for="item in data"
        :key="item"
        :data="item"
        @remove="remove($event)"
      />
      <Item
        v-if="isAdding"
        :readonly="false"
        @added="added"
        @cancel="isAdding=false"
      />
      <el-button
        @click="isAdding=true"
        icon="el-icon-plus"
        size="mini"
      />
    </div>
  </el-card>
</template>

<script>
import Item from './Item'
import { useStore } from 'vuex'
import { ref } from 'vue'
import { ElMessage } from 'element-plus'

export default {
  name: 'FrequencyRule',
  components: {
    Item
  },
  setup () {
    const store = useStore()

    const data = store.state.frequencyRule.data

    const isAdding = ref(false)

    const remove = (ruleId) => {
      store.dispatch('frequencyRule/remove', ruleId)
      ElMessage({
        type: 'success',
        message: '删除成功',
        showClose: true
      })
    }

    const added = () => {
      isAdding.value = false
      ElMessage({
        type: 'success',
        message: '添加成功',
        showClose: true
      })
    }

    return {
      data,
      isAdding,
      remove,
      added
    }
  }
}
</script>

<style lang="less" scoped>
@import "/src/assets/css/color.less";

#frequency-rule{
  #title{
    display: flex;
    color: @black2;
    justify-content: center;
  }

  #content{
    height: 100%;
  }
}
</style>
