<template>
  <el-dialog
    title="添加计划"
    v-model="show"
    width="35%"
  >
    <el-form
      :model="data"
      :rules="rules"
    >
      <el-form-item
        label="开始时间"
        prop="startDate"
      >
        <el-date-picker v-model="data.startDate" />
      </el-form-item>
      <el-form-item
        label="结束时间"
        prop="endDate"
      >
        <el-date-picker v-model="data.endDate" />
      </el-form-item>
      <el-form-item
        label="名称"
        prop="name"
      >
        <el-input
          v-model="data.name"
          style="width: 15em;"
          clearable
        />
      </el-form-item>
      <el-form-item
        label="计划类型"
        prop="taskType"
      >
        <TaskType v-model="data.taskType" />
      </el-form-item>
      <el-form-item
        label="预期时间"
        prop="expectedTime"
      >
        <el-input-number
          v-model="data.expectedTime"
          :min="0"
          :step="5"
        />
      </el-form-item>
      <el-form-item
        label="子任务"
        prop="subTasks"
      >
        <SubTasks :data="data.subTasks" />
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="show = false">取 消</el-button>
        <el-button
          type="primary"
          @click="add"
        >确 定</el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import { VModel } from '@/utils'
import { reactive, watch } from 'vue'
import TaskType from '@/components/TaskType'
import SubTasks from '@/components/SubTasks'
import { planHandleProxy } from '@/views/Plan/LimitedPlan/DateView/planProxy'

export default {
  name: 'AddPlanPopup',
  components: {
    TaskType,
    SubTasks
  },
  props: {
    modelValue: {
      type: Boolean,
      default: false
    },
    date: {
      type: Object,
      required: true
    }
  },
  emits: ['update:modelValue'],
  setup (props, ctx) {
    const show = VModel(props, ctx)

    const data = reactive({
      startDate: props.date,
      endDate: props.date,
      name: '',
      expectedTime: 0,
      taskType: null,
      subTasks: []
    })

    watch(() => props.date, () => {
      data.startDate = props.date
      data.endDate = props.date
    })

    const addOneDay = (date) => {
      const day = date.getDate() + 1
      date.setDate(day)
      date.setHours(11)
      return date
    }

    const add = () => {
      const plan = {
        ...data,
        endDate: addOneDay(data.endDate)
      }
      planHandleProxy.addPlan(plan)
      show.value = false
    }

    const rules = {
      name: [
        { required: true, message: '请输入名称', trigger: 'change' }
      ],
      taskType: [
        { required: true, message: '请输入类型', trigger: 'change' }
      ],
      startDate: [
        { required: true, message: '请输入开始时间', trigger: 'change' }
      ],
      endDate: [
        { required: true, message: '请输入结束时间', trigger: 'change' }
      ]
    }

    return {
      show,
      add,
      data,
      rules
    }
  }
}
</script>

<style lang="less" scoped>
.el-form{
  display: flex;
  flex-direction: column;

  .el-form-item{
    display: flex;

    /deep/label{
      width: 7em;
    }
  }
}
</style>
