<template>
  <div
    id="not-selected"
    :class="[isNegate?'negative':'positive']"
  >
    <span @click="isNegate=!isNegate" />
    <div id="content">
      <slot />
    </div>
    <i
      id="close"
      class="el-icon-close"
      @click="clear"
    />
  </div>
</template>

<script>
import { VModel } from '@/utils'

export default {
  name: 'NotButton',
  props: {
    modelValue: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue', 'clear'],
  setup (props, ctx) {
    const isNegate = VModel(props, ctx)

    const clear = () => {
      ctx.emit('clear')
    }

    return {
      isNegate,
      clear
    }
  }
}
</script>

<style lang="less" scoped>
@import "/src/assets/css/color.less";
#not-selected{
  display: flex;
  flex-direction: row;
  align-items: center;
  border-style: solid;
  border-width: 1px;
  border-radius: 0.25em;
  font-size: 14px;
  padding: 0.5em;
  background-color: white;

  &:hover #close{
    color: @black4;
  }

  >:nth-child(1){
    width: 1em;
    height: 1em;
    margin-right: 0.5em;
    border-radius: 50%;
    position: relative;
    cursor: pointer;
    border-style: solid;
    border-width: 1px;
    transition: 200ms;
    flex-shrink: 0;

    &::after{
      content: '';
      width: 0.5em;
      height: 0.5em;
      position: absolute;
      left: 50%;
      top: 50%;
      border-radius: 50%;
      transform: translate(-50%, -50%);
      transition: 200ms;
    }
  }
  >:nth-child(2){
    cursor: text;
    margin-right: 0.5em;
  }

  #close{
    cursor: pointer;
    color: transparent;
    margin-left: auto;

    &:hover{
      color: @red;
    }
  }
}

.positive{
  border-color: @blue;
  color: @blue;

  >:nth-child(1){
    border-color: @blue;
    background: whitesmoke;

    &::after{
      background: @blue;
    }
  }
}

.negative{
  border-color: @red;
  color: @red;

  >:nth-child(1){
    border-color: @red;
    background: @red;

    &::after{
      background: whitesmoke;
    }
  }
}
</style>
