<template>
  <el-dialog
    title="任务模板"
    v-model="popupVisibility.value"
    width="35%"
    @closed="onClosePopup"
  >
    <div
      v-if="!state.popupEditable"
      id="readonly-panel"
    >
      <div>
        <span>名称</span>
        <span>{{ template.name }}</span>
      </div>
      <div>
        <span>任务类型</span>
        <span>{{ getTaskTypeFullName(template.taskType) }}</span>
      </div>
      <div>
        <span>预期时间</span>
        <span>{{ template.time }}分钟</span>
      </div>
      <div>
        <span>子任务</span>
        <div>
          <div
            v-for="subTask in template.subTasks"
            :key="subTask._id"
            class="sub-task"
          >
            <span>{{ subTask.name }}</span>
            <span>预期时间 {{ subTask.expectedTime }} 分钟</span>
          </div>
        </div>
      </div>
    </div>
    <el-form
      v-else
      :model="template"
      :rules="rules"
    >
      <el-form-item
        label="名称"
        prop="name"
      >
        <el-input
          v-model="template.name"
          clearable
        />
      </el-form-item>
      <el-form-item
        label="任务类型"
        prop="taskType"
      >
        <TaskType v-model="template.taskType" />
      </el-form-item>
      <el-form-item
        label="预期时间"
        prop="time"
      >
        <el-input-number
          v-model="template.time"
          :disabled="template.subTasks.length!==0"
          :min="0"
          :step="5"
        />
      </el-form-item>
      <el-form-item
        label="子任务"
        prop="subTasks"
      >
        <SubTasks
          :data="template.subTasks"
          v-model:expected-time="template.time"
        />
      </el-form-item>
    </el-form>

    <template #footer>
      <span class="dialog-footer">
        <el-button @click="cancel">
          取 消
        </el-button>

        <el-button
          v-show="state.popupEditable"
          @click="remove"
          type="danger"
          plain
        >
          删 除
        </el-button>

        <el-button
          v-show="state.popupEditable"
          type="primary"
          @click="confirm"
        >
          确 定
        </el-button>

        <el-button
          v-show="!state.popupEditable"
          type="primary"
          @click="modify"
        >
          修 改
        </el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import { computed, ref } from 'vue'
import { useStore } from 'vuex'
import SubTasks from '@/components/SubTasks'
import TaskType from '@/components/TaskType'
import _ from 'lodash'
import { getTaskTypeFullName } from '@/utils/taskType'

export default {
  name: 'DetailPopup',
  components: {
    SubTasks,
    TaskType
  },
  setup () {
    const store = useStore()
    const state = store.state.task
    const popupVisibility = state.popupVisibility
    const newTemplate = ref({})

    const cancel = () => {
      popupVisibility.value = false
    }

    const modify = () => {
      newTemplate.value = _.cloneDeep(state.currentTemplate)
      state.popupEditable = true
    }

    const onClosePopup = () => {
      state.popupEditable = false
      state.currentTemplate = null
    }

    const remove = () => {
      if (state.popupVisibility.type === 0) {
        store.dispatch('task/removeCommonTemplate', state.currentTemplate.id)
          .then(_ => {
            cancel()
          })
      } else {
        store.dispatch('task/removeCustomTemplate', state.currentTemplate.id)
          .then(_ => {
            cancel()
          })
      }
    }

    const confirm = () => {
      if ('id' in state.currentTemplate) {
        if (!_.isEqual(state.currentTemplate, newTemplate.value)) {
          if (popupVisibility.type === 0) {
            store.dispatch('task/updateCommonTemplate', newTemplate.value)
          } else {
            store.dispatch('task/updateCustomTemplate', newTemplate.value)
          }
        }
      } else {
        if (popupVisibility.type === 0) {
          store.dispatch('task/addCommonTemplate', state.currentTemplate)
        } else {
          store.dispatch('task/addCustomTemplate', state.currentTemplate)
        }
      }
      popupVisibility.value = false
    }

    const template = computed(() => {
      if (!state.popupEditable) {
        return state.currentTemplate
      }
      if ('id' in state.currentTemplate) {
        return newTemplate.value
      } else {
        return state.currentTemplate
      }
    })

    const rules = {
      name: [
        { required: true, message: '请输入名称', trigger: 'change' }
      ],
      taskType: [
        { required: true, message: '请输入类型', trigger: 'change' }
      ]
    }

    return {
      state,
      popupVisibility,
      newTemplate,
      modify,
      remove,
      cancel,
      confirm,
      template,
      getTaskTypeFullName,
      onClosePopup,
      rules
    }
  }
}
</script>

<style lang="less" scoped>

.el-form{
  .el-form-item{
    display: flex;

    /deep/label{
      width: 7em;
      text-align: left;
    }

    /deep/.el-form-item__content{
      >*{
        margin-right: 0.5em;
      }
    }
  }
}

#readonly-panel{
  >div{
    display: flex;
    align-items: center;
    margin-bottom: 1em;

    span{
      font-size: 16px;
    }

    >span{
      margin-right: 0.5em;
    }

    >span:nth-child(1){
      width: 5em;
    }

    .sub-task{
      margin-bottom: 0.5em;
      >*{
        margin-right: 1.5em;
      }
    }
  }

  >:last-child{
    align-items: unset;
  }
}
</style>
