<template>
  <el-popover
    placement="right"
    trigger="hover"
    popper-class="popup"
  >
    <template #reference>
      <NotSelect
        v-model="isNegate"
        @clear="clear"
        class="select"
      >
        <slot>
          <span v-if="formatDateUnion(dateUnion)">
            {{ formatDateUnion(dateUnion) }}
          </span>
          <span
            v-else
            id="placeholder"
          >
            {{ placeholder[repeatType] }}
          </span>
        </slot>
      </NotSelect>
    </template>
    <div id="panel">
      <div>
        <el-radio-group
          v-model="repeatType"
          @change="onChangeRepeatType($event)"
          size="mini"
        >
          <el-radio-button :label="0">
            <i class="el-icon-date" />
          </el-radio-button>
        </el-radio-group>
        <el-radio-group
          v-model="repeatType"
          @change="onChangeRepeatType($event)"
          size="mini"
        >
          <el-radio-button :label="1">
            每天
          </el-radio-button>
          <el-radio-button :label="2">
            每周
          </el-radio-button>
          <el-radio-button :label="3">
            每月
          </el-radio-button>
        </el-radio-group>
      </div>
      <div>
        <div
          v-show="repeatType===0"
          id="date"
        >
          <DatePicker
            v-model="dates"
            is-range
          />
        </div>
        <div
          v-show="repeatType===2"
          id="week"
        >
          <span
            v-for="(item, index) in ['一','二','三','四','五','六','日']"
            :key="index"
            @click="onClickWeekDay(index)"
            :class="[weekDay[index]?'active':'default']"
            class="week-day"
          >
            {{ item }}
          </span>
        </div>
        <div
          v-show="repeatType===3"
          id="month"
        >
          <span
            v-for="index in 35"
            :key="index"
            @click="onClickMonthDay(index)"
            :class="[monthDay[index]?'active':'default']"
            class="month-day"
          >
            {{ index }}
          </span>
        </div>
      </div>
    </div>
  </el-popover>
</template>

<script>
import { reactive, ref, watch } from 'vue'
import NotSelect from '@/components/select/NotSelect'
import { DatePicker } from 'v-calendar'
import _ from 'lodash'
import { MultiVModel } from '@/utils'
import dayjs from 'dayjs'

export default {
  name: 'DateSelect',
  props: {
    date: {
      type: Object,
      default: () => {}
    },
    negate: {
      type: Boolean,
      default: false
    },
    type: {
      type: Number,
      default: 0
    }
  },
  emits: ['update:date', 'update:negate', 'update:type'],
  components: {
    NotSelect,
    DatePicker
  },
  setup (props, ctx) {
    // 其中有三种可能的类型：具体日期、周、月
    // 切换到对应的类型会切换到不同的值
    const dateUnion = MultiVModel(props, ctx, 'date')
    const isNegate = MultiVModel(props, ctx, 'negate')
    const repeatType = MultiVModel(props, ctx, 'type')

    // NOTE: 必须用ref，reactive没有反应
    const dates = ref({
      start: null,
      end: null
    })
    watch(dates, () => {
      dateUnion.value = dates.value
    })

    const weekDay = reactive(new Array(7))
    _.fill(weekDay, false)
    if (repeatType.value === 2) {
      for (const e of dateUnion.value) {
        weekDay[e - 1] = true
      }
    }
    const onClickWeekDay = (i) => {
      weekDay[i] = !weekDay[i]
    }

    const monthDay = reactive(new Array(31))
    _.fill(monthDay, false)
    if (repeatType.value === 3) {
      for (const e of dateUnion.value) {
        weekDay[e - 1] = true
      }
    }
    const onClickMonthDay = (i) => {
      monthDay[i] = !monthDay[i]
    }

    const onChangeRepeatType = (type) => {
      switch (type) {
        case 0: // 具体日期
          dateUnion.value = dates.value
          break
        case 1: // 日
          dateUnion.value = null
          break
        case 2: // 周
          dateUnion.value = weekDay
          break
        case 3: // 月
          dateUnion.value = monthDay
          break
      }
    }

    const weekValue = ['一', '二', '三', '四', '五', '六', '日']

    const placeholder = ['请输入日期', '已选择每一天', '请选择周几', '请选择几号']

    const formatDateUnion = (date) => {
      console.log(date)
      switch (repeatType.value) {
        case 0: // 具体日期
          if (date && date.start) {
            return dayjs(date.start).format('YYYY/MM/DD') + '-' + dayjs(date.end).format('YYYY/MM/DD')
          }
          return null
        case 1: // 日
          return null
        case 2: // 周
          return weekDay.reduce((str, cur, index) => {
            if (cur) {
              return str.concat(weekValue[index] + '、')
            }
            return str
          }, '').slice(0, -1)
        case 3: // 月
          return monthDay.reduce((str, cur, index) => {
            if (cur) {
              return str.concat(index + ', ')
            }
            return str
          }, '').slice(0, -2)
      }
    }

    const clear = () => {
      switch (repeatType.value) {
        case 0: // 具体日期
          dates.value = {}
          break
        case 1: // 日
          break
        case 2: // 周
          _.fill(weekDay, false)
          break
        case 3: // 月
          _.fill(monthDay, false)
          break
      }
    }

    return {
      dateUnion,
      dates,
      weekDay,
      monthDay,
      isNegate,
      repeatType,
      placeholder,
      onChangeRepeatType,
      onClickWeekDay,
      onClickMonthDay,
      formatDateUnion,
      clear
    }
  }
}
</script>

<style lang="less" scoped>
@import "/src/assets/css/color.less";
/deep/ .el-range-editor--mini.el-input__inner {
  width: 15em;
}

/deep/ .el-date-editor .el-range-input {
  width: 7em;
}

/deep/ .vc-container{
  border: solid 0 transparent;
  border-radius: 0.4em;
}

.select{
  width: 15em;
}

#placeholder{
  color: @black4;
}

#panel {
  display: flex;
  flex-direction: column;
  width: 250px;

  > div {
    display: flex;
    flex-direction: row;
    width: 100%;

    > :first-child {
      margin-right: auto;
    }
  }

  >:first-child{
    background-color: white;
    border-radius: 0.4em;
  }

  .transition {
    position: absolute;
    top: 30px;
    left: 0;
  }

  #date, #week, #month{
    display: flex;
  }

  #week, #month{
    width: 100%;
    border-radius: 0.4em;
    background-color: white;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 0.5em;
  }

  #month{
    >:nth-last-child(1), >:nth-last-child(2), >:nth-last-child(3), >:nth-last-child(4){
      visibility: hidden;
    }
  }

  .week-day, .month-day{
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    width: 2.5em;
    height: 2.5em;
    margin-bottom: 0.35em;
    font-size: small;
    border-radius: 0.4em;
    cursor: pointer;
    transition: 200ms;

    &::selection{
      background-color: transparent;
    }
  }
}

.default{
  background-color: white;
  color: #606266;
  border: solid 1px @black4;
  //box-shadow: 0 0 3px rgba(100, 100, 100, 0.42);
}

.active{
  background-color: #409EFF;
  color: white;
  box-shadow: 0 0 3px rgba(64, 158, 255, 0.68);
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 2s ease;
}
.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
</style>
