<template>
  <Base>
    <slot>
      <NotSelect
        v-model="data.isNegate"
        class="not-select"
      >
        <slot>
          <TaskType
            v-model="data.type"
            size="medium"
            class="type-select"
          />
        </slot>
      </NotSelect>
    </slot>
  </Base>
</template>

<script>
import { inject } from 'vue'
import Base from './Base'
import TaskType from '@/components/TaskType'
import NotSelect from '@/components/select/NotSelect'

export default {
  name: 'TypeFilter',
  components: {
    Base,
    TaskType,
    NotSelect
  },
  setup () {
    const data = inject('data')

    return {
      data
    }
  }
}
</script>

<style lang="less" scoped>
.not-select{
  padding-bottom: 0!important;
  padding-top: 0!important;
  width: 15em;

  /deep/ #close{
    display: none;
  }
}
.type-select{
  width: 13.5em;
  /deep/.el-input__inner{
    border: none;
  }
}
</style>
