<template>
  <el-dialog
    title="修改顺序限制计划"
    v-model="show"
    width="35%"
  >
    <el-form
      :model="data"
      :rules="rules"
    >
      <el-form-item
        label="名称"
        prop="name"
      >
        <el-input
          v-model="data.name"
          clearable
        />
      </el-form-item>
      <el-form-item
        label="任务类型"
        prop="taskType"
      >
        <TaskType v-model="data.taskType" />
      </el-form-item>
      <el-form-item
        label="持续时间"
        prop="time"
      >
        <el-input-number
          v-model="data.time"
          :min="0"
          :step="5"
          :disabled="data.subTasks.length!==0"
        />
      </el-form-item>
      <el-form-item
        label="子任务"
        prop="subTasks"
      >
        <SubTasks
          :data="data.subTasks"
          v-model:expected-time="data.time"
        />
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="show = false">取 消</el-button>
        <el-button
          type="primary"
          @click="update"
        >确 定</el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import { VModel } from '@/utils'
import TaskType from '@/components/TaskType'
import SubTasks from '@/components/SubTasks'
import planDetails from '@/store/planDetails'
import { watch, ref } from 'vue'
import { planHandleProxy } from '@/views/Plan/LimitedPlan/TimeView/planProxy'
import _ from 'lodash'

export default {
  name: 'UpdateOrderLimitedPlanPopup',
  components: {
    TaskType,
    SubTasks
  },
  props: {
    modelValue: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue'],
  setup (props, ctx) {
    const show = VModel(props, ctx)
    const data = ref({})

    watch(show, () => {
      if (show.value) {
        data.value = _.cloneDeep(planDetails.get())
      }
    })

    const update = () => {
      planHandleProxy.updateOrderLimitedPlan(data.value)
      planDetails.set(data.value)
      show.value = false
    }

    const rules = {
      name: [
        { required: true, message: '请输入名称', trigger: 'change' }
      ],
      taskType: [
        { required: true, message: '请输入类型', trigger: 'change' }
      ]
    }

    return {
      data,
      show,
      update,
      rules
    }
  }
}
</script>

<style lang="less" scoped>
.el-form{
  display: flex;
  flex-direction: column;

  .el-form-item{
    display: flex;

    /deep/label{
      width: 7em;
    }
  }
}
</style>
