<template>
  <div id="plan">
    <div id="plan-panel">
      <div
        v-if="mode==='time'"
        id="time"
      >
        <el-date-picker
          v-model="date"
          format="YYYY 年  M 月  D 日"
          size="medium"
        />
        <TimeView :date="date" />
      </div>
      <div v-else-if="mode==='date'">
        <DateView :date="date" />
      </div>
      <div v-else>
        <UnlimitedPlan />
      </div>
    </div>
    <el-radio-group
      v-model="mode"
      id="mode-select"
      size="medium"
    >
      <el-radio-button label="time">
        时间
      </el-radio-button>
      <el-radio-button label="date">
        日期
      </el-radio-button>
      <el-radio-button label="unlimited">
        无限制
      </el-radio-button>
    </el-radio-group>
    <PlanDetails id="detail" />
  </div>
</template>

<script>
import DateView from './LimitedPlan/DateView/Index'
import TimeView from './LimitedPlan/TimeView/Index'
import UnlimitedPlan from './UnlimitedPlan/Index'
import PlanDetails from './PlanDetails/Index'
import { ref } from 'vue'

export default {
  name: 'Plan',
  components: {
    DateView,
    TimeView,
    PlanDetails,
    UnlimitedPlan
  },
  setup () {
    const date = ref(new Date())
    const mode = ref('time')
    return {
      date,
      mode
    }
  }
}
</script>

<style lang="less" scoped>

#plan {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-between;

  #plan-panel {
    width: 80%;
    height: 100%;

    >div{
      height: 100%;
    }

    #time{
      display: flex;
      flex-direction: column;
    }
  }

  #detail {
    width: 19%;
    height: 100%;
  }

  #mode-select {
    position: absolute;
    top: 0;
    right: 20%;
    height: 7%;
    z-index: 999;
    display: flex;
    align-items: center;
    margin-right: 10px;
  }
}
</style>
