<template>
  <el-select
    v-model="data"
    placeholder=" "
    size="medium"
  >
    <el-option
      v-for="item in operator"
      :key="item.value"
      :value="item.value"
      :label="item.label"
    />
  </el-select>
</template>

<script>
import { VModel } from '@/utils'
import { OPERATOR, OPERATOR_OPTIONS } from '@/interface/enum'

export default {
  name: 'OperatorButton',
  props: {
    modelValue: {
      type: Number,
      default: OPERATOR.EQ
    }
  },
  emits: ['update:modelValue'],
  setup (props, ctx) {
    return {
      data: VModel(props, ctx),
      operator: OPERATOR_OPTIONS
    }
  }
}
</script>

<style lang="less" scoped>
.el-select{
  width: 5em;
}
</style>
