<template>
  <el-button
    @click="add"
    icon="el-icon-plus"
    type="primary"
    plain
    size="mini"
  />
</template>

<script>
import { useStore } from 'vuex'
export default {
  name: 'AddButton',
  props: {
    type: {
      type: Number,
      required: true
    }
  },
  setup (props) {
    const state = useStore().state.task
    const popupVisibility = state.popupVisibility

    const add = () => {
      state.currentTemplate = createNullTemplate()
      popupVisibility.type = props.type
      state.popupEditable = true
      popupVisibility.value = true
    }

    const createNullTemplate = () => {
      return {
        name: '',
        icon: 'el-icon-eleme',
        time: null,
        taskType: [],
        subTasks: []
      }
    }

    return {
      add
    }
  }
}
</script>

