<template>
  <el-dialog
    title="删除关系"
    v-model="show"
    width="30%"
  >
    <div>
      确认要删除该关系么？
    </div>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="show = false">取 消</el-button>
        <el-button
          type="primary"
          @click="remove"
        >确 定</el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import { VModel } from '@/utils'
import planDetails from '@/store/planDetails'
import { planHandleProxy } from '@/views/Plan/LimitedPlan/TimeView/planProxy'

export default {
  name: 'RemoveRelationPopup',
  props: {
    modelValue: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue'],
  setup (props, ctx) {
    const show = VModel(props, ctx)
    const remove = () => {
      const id = planDetails.get().id
      planHandleProxy.removeRelation(id)
      planDetails.clear()
      show.value = false
    }
    return {
      show,
      remove
    }
  }
}
</script>

