<template>
  <el-card shadow="hover">
    <template #header>
      <div id="card-header">
        <span>{{ data.name }}</span>
        <el-button
          type="text"
          @click="apply"
        >
          应用
        </el-button>
      </div>
    </template>
    <div id="card-content">
      <i
        class="icon"
        :class="data.icon"
      />
      <div>
        <span>{{ getTaskTypeFullName(data.taskType) }}</span>
        <el-button
          type="text"
          icon="el-icon-more"
          @click="showDetail"
        />
      </div>
    </div>
  </el-card>
</template>

<script>
import { useStore } from 'vuex'
import { getTaskTypeFullName } from '@/utils/taskType'

export default {
  name: 'TaskTemplateItem',
  props: {
    data: {
      type: Object,
      required: true
    },
    type: {
      type: Number,
      required: true
    }
  },
  setup (props) {
    const store = useStore()
    const state = store.state.task

    const apply = () => {
      state.currentTask.name = props.data.name
      state.currentTask.taskType = props.data.taskType
      state.currentTask.expectedTime = props.data.time
      state.currentTask.subTasks = props.data.subTasks
    }

    const showDetail = () => {
      state.currentTemplate = props.data
      state.popupVisibility.type = props.type
      state.popupVisibility.value = true
    }

    return {
      apply,
      showDetail,
      getTaskTypeFullName
    }
  }
}
</script>

<style lang="less" scoped>
@import "/src/assets/css/color.less";

.el-card{
  width: 24%;
  margin-bottom: 0.5em;

  /deep/.el-card__header{
    padding-top: 0;
    padding-bottom: 0;
  }

  /deep/.el-card__body{
    padding-bottom: 0.5em;
  }

  #card-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;
  }

  #card-content{
    i{
      color: @black2;
      font-size: 5em;
      display: flex;
      justify-content: center;
    }
    >div{
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .el-button{
      color: @black3;
    }
  }
}

#template-item{
  display: flex;
  flex-direction: column;
  width: 7em;
  border-radius: 1em;
  border: solid 2px #646464;
  padding: 0.5em;
  margin: 0.5em;
  cursor: pointer;
  transition: border 200ms;

  &:hover {
    transition: border 200ms;
    border-color: @blue;
    >*{
      transition: 200ms;
      color: @blue;
    }
  }

  >:nth-child(1){
    font-size: 0.8em;
    display: flex;
    flex-direction: row;
    align-items: center;
    transition: 200ms;

    >:nth-child(1){
      margin-right: auto;
    }
    >:nth-child(2){
      color: unset;

      &:hover{
        font-weight: bold;
        color: @red;
      }
    }
  }
  >:nth-child(2){
    align-self: center;
    transition: 200ms;
  }
  >:nth-child(3){
    align-self: center;
    transition: 200ms;
  }
  .icon{
    font-size: 3em;
  }
}
</style>
