<template>
  <el-dialog
    title="添加顺序计划"
    v-model="show"
    width="35%"
  >
    <el-form
      :model="data"
      :rules="rules"
    >
      <el-form-item
        label="在此计划"
        prop="order"
      >
        <el-radio
          v-model="data.order"
          :label="0"
        >
          之前
        </el-radio>
        <el-radio
          v-model="data.order"
          :label="1"
        >
          之后
        </el-radio>
      </el-form-item>
      <el-form-item
        label="名称"
        prop="name"
      >
        <el-input
          v-model="data.name"
          style="width: 15em;"
          clearable
        />
      </el-form-item>
      <el-form-item
        label="任务类型"
        prop="taskType"
      >
        <TaskType v-model="data.taskType" />
      </el-form-item>
      <el-form-item
        label="持续时间"
        prop="time"
      >
        <el-input-number
          v-model="data.time"
          :min="0"
          :step="5"
          :disabled="data.subTasks.length!==0"
        />
      </el-form-item>
      <el-form-item
        label="子任务"
        prop="subTasks"
      >
        <SubTasks
          :data="data.subTasks"
          v-model:expected-time="data.time"
        />
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="show = false">取 消</el-button>
        <el-button
          type="primary"
          @click="submit"
        >确 定</el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import { VModel } from '@/utils'
import { planHandleProxy } from '@/views/Plan/LimitedPlan/TimeView/planProxy'
import TaskType from '@/components/TaskType'
import SubTasks from '@/components/SubTasks'
import { ElMessage } from 'element-plus'
import { reactive } from 'vue'
export default {
  name: 'AddOrderLimitedPlanPopup',
  components: {
    TaskType,
    SubTasks
  },
  props: {
    modelValue: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue'],
  setup (props, ctx) {
    const show = VModel(props, ctx)
    const data = reactive({
      order: 0,
      name: '',
      time: 0,
      taskType: [],
      subTasks: []
    })

    const submit = () => {
      if (name.value !== '') {
        planHandleProxy.addOrderLimitedPlan(data)
        show.value = false
      } else {
        ElMessage({
          type: 'error',
          message: '请输入计划名称',
          showClose: true
        })
      }
    }

    const rules = {
      name: [
        { required: true, message: '请输入名称', trigger: 'change' }
      ],
      taskType: [
        { required: true, message: '请输入类型', trigger: 'change' }
      ]
    }

    return {
      show,
      name,
      data,
      submit,
      rules
    }
  }
}
</script>

<style lang="less" scoped>
.el-form{
  display: flex;
  flex-direction: column;

  .el-form-item{
    display: flex;

    /deep/label{
     width: 7em;
    }
  }
}
</style>
