<template>
  <div id="finish">
    <div>
      <div id="task-info">
        <TaskType
          v-model="state.taskType"
          :readonly="true"
        />
        <span>{{ state.name }}</span>
        <span>有效时间 {{ state.time }} 分钟</span>
        <span v-if="state.expectedTime!==0">预期 {{ state.expectedTime }} 分钟</span>
      </div>
      <div id="task-time">
        <span>{{ formatTime(state.startTime) }} - {{ formatTime(state.endTime) }}</span>
      </div>
      <div id="sub-tasks">
        <div
          v-for="(item, index) in state.subTasks"
          :key="index"
          v-once
          style="display: flex;"
        >
          <span style="width: 12em;">{{ item.name }}</span>
          <span>进行 {{ getMinute(item.time) }} 分钟</span>
        </div>
      </div>
    </div>
    <div>
      <div id="devotion">
        <span> 投入度：</span>
        <el-rate v-model="state.devotion" />
      </div>
      <div id="satisfaction">
        <span>满意度：</span>
        <el-rate v-model="state.satisfaction" />
      </div>
      <el-input
        v-model="state.note"
        type="textarea"
        :rows="20"
        placeholder="笔记"
      />

      <el-button @click="finish">
        完 成
      </el-button>
    </div>
    <div>
      <Track :data="state.track" />
    </div>
  </div>
</template>

<script>
import { useRouter } from 'vue-router'
import Track from '@/components/Track'
import TaskType from '@/components/TaskType'
import TimeManager from '@/utils/time'
import { useStore } from 'vuex'
import dayjs from 'dayjs'

export default {
  name: 'Finish',
  components: {
    Track,
    TaskType
  },
  setup () {
    const router = useRouter()

    const store = useStore()
    const state = store.state.task.currentTask

    const finish = () => {
      store.dispatch('task/addTask', state)
        .then(_ => {
          router.push({
            name: 'Start'
          })
        })
    }

    const formatTime = (time) => {
      return dayjs(time).format('YYYY/MM/DD HH:mm')
    }

    return {
      state,
      name,
      finish,
      getMinute: TimeManager.second2Minute,
      formatTime
    }
  }
}
</script>

<style lang="less" scoped>
@import "/src/assets/css/color.less";

#finish{
  display: flex;
  >:nth-child(1){
    width: 33%;

    #task-info{
      display: flex;
      align-items: center;
      margin-bottom: 1em;

      >*{
        margin-right: 1em;
      }
    }

    #sub-tasks{
      >div{
        margin-top: 1em;

        >:first-child{
          margin-right: 0.5em;
        }
      }
    }
  }

  >:nth-child(2){
    width: 33%;
    display: flex;
    flex-direction: column;
    align-items: center;

    #devotion{
      color: @black2;
      font-size: 2em;
      display: flex;
      flex-direction: row;
      align-items: center;
      margin-bottom: 1em;
    }

    #satisfaction{
      color: @black2;
      font-size: 2em;
      display: flex;
      flex-direction: row;
      align-items: center;
      margin-bottom: 1em;
    }

    .el-rate{
      height: unset;
    }

    /deep/.el-rate__icon{
      font-size: 36px;
    }

    .el-button{
      margin-top: 1em;
    }
  }

  >:nth-child(3){
    width: 33%;
    >*{
      margin-left: 5em;
    }
  }
}
</style>
