<template>
  <div id="date-view">
    <div
      ref="calendarEl"
    />
    <AddPlanPopup
      v-model="showAddPlanPopup"
      :date="selectedDate"
    />
  </div>
</template>

<script>
import { onMounted, ref, watch } from 'vue'
import { useStore } from 'vuex'
import AddPlanPopup from '../../LimitedPlan/popup/AddPlanPopup'
import { planDrawerProxy, planHandleProxy } from './planProxy'

export default {
  name: 'DateView',
  components: {
    AddPlanPopup
  },
  props: {
    date: {
      type: Object,
      required: true
    }
  },
  setup (props) {
    const store = useStore()
    const calendarEl = ref(null)

    const showAddPlanPopup = ref(false)

    const selectedDate = ref(new Date())

    watch(() => props.date, () => {
      planHandleProxy.setDate(props.date)
    })

    onMounted(() => {
      planDrawerProxy.init(calendarEl.value)
      planHandleProxy.init(store)
      planHandleProxy.onDateClick(info => {
        selectedDate.value = info.date
        showAddPlanPopup.value = true
      })
      planDrawerProxy.renderEvents(props.date)
    })

    return {
      calendarEl,
      showAddPlanPopup,
      selectedDate
    }
  }
}
</script>

<style lang="less" scoped>
@import "/src/assets/css/color.less";

#date-view{
  height: 100%;

  >:first-child{
    height: 100%;
  }
}

/deep/.fc-button-primary{
  background-color: white;
  color: @black2;
  border: none;

  &:focus{
    box-shadow: unset;
  }

  &:hover{
    background-color: @blue2;
  }

  &:disabled{
    background-color: @black3;
  }
}

/deep/.fc-button-primary:not(:disabled).fc-button-active{
  background-color: @blue;
  color: white;

  &:focus{
    box-shadow: unset;
  }
}

/deep/.fc-button-primary:not(:disabled):active{
  background-color: @blue;
  color: white;
}

/deep/.fc-button:focus{
  box-shadow: unset;
}
</style>
