<template>
  <div id="sub-tasks">
    <draggable
      :list="data"
      tag="transition-group"
      @start="drag=true"
      @end="drag=false"
      item-key="_id"
      animation="300"
      handle=".el-icon-sort"
    >
      <template #item="{element}">
        <div class="sub-task">
          <i class="el-icon-sort" />

          <el-input
            class="input-text"
            v-model="element.name"
            placeholder="子任务名称"
            clearable
          />

          <div class="expected-time">
            <span>预期时间</span>
            <el-input-number
              v-model="element.expectedTime"
              :step="5"
              :min="0"
            />
          </div>

          <i
            class="el-icon-close"
            @click="remove(element)"
          />
        </div>
      </template>
    </draggable>
    <div id="button-group">
      <i class="el-icon-sort" />
      <el-button
        @click="add"
        icon="el-icon-plus"
        type="primary"
        plain
      />
    </div>
  </div>
</template>

<script>
import { ref, watch } from 'vue'
import draggable from 'vuedraggable'

export default {
  name: 'SubTasks',
  components: {
    draggable
  },
  props: {
    data: {
      type: Array,
      required: true
    }
  },
  emits: ['update:expectedTime'],
  setup (props, ctx) {
    const subTasks = ref(props.data)

    watch(() => props.data, () => {
      subTasks.value = props.data
    })

    let id = -1
    const createItem = () => ({
      _id: id--,
      name: '',
      time: 0,
      expectedTime: 0
    })

    const add = () => {
      subTasks.value.push(createItem())
    }

    const remove = (subTask) => {
      let i = 0
      while (subTasks.value[i]._id !== subTask._id) i++
      subTasks.value.splice(i, 1)
    }

    watch(() => props.data, () => {
      if (props.data.length === 0) {
        return
      }
      let _expectedTime = 0
      if (subTasks.value.length !== 0) {
        for (const subTask of subTasks.value) {
          if (subTask.expectedTime !== null) {
            _expectedTime += subTask.expectedTime
          }
        }
      }
      ctx.emit('update:expectedTime', _expectedTime)
    }, { deep: true })

    return {
      subTasks,
      add,
      remove
    }
  }
}
</script>

<style lang="less" scoped>
@import "/src/assets/css/color.less";

.sub-task {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 1em 0;

  .el-icon-sort{
    color: @gray;

    &:hover {
      color: @blue;
      cursor: pointer;
    }
  }

  .el-icon-close {
    color: @black3;

    &:hover {
      color: @red;
      cursor: pointer;
    }
  }

  .input-text {
    width: 20em;
  }

  .expected-time{
    display: flex;
    align-items: center;
    margin: 0 1em;

    >span{
      color: @black2;
      margin-right: 0.7em;
      flex-shrink: 0;
    }

    .el-input-number{
      width: 9em;
    }
  }
}

#button-group{
  display: flex;
  align-items: center;
  margin-top: 1em;

  i{
    color: transparent;
  }

  .el-button{
    width: 20em;
  }
}

</style>
