<template>
  <div
    id="unlimited-plan-item"
    @click="onClick"
  >
    <span>{{ getTaskTypeFullName(data.typeList) }}</span>
    <span>{{ data.planName }}</span>
  </div>
</template>

<script>
import { getTaskTypeFullName } from '@/utils/taskType'
import planDetails from '@/store/planDetails'

export default {
  name: 'UnlimitedPlanItem',
  components: {},
  props: {
    data: {
      type: Object,
      required: true
    }
  },
  setup (props) {
    const onClick = () => {
      planDetails.set(props.data)
    }

    return {
      getTaskTypeFullName,
      onClick
    }
  }
}
</script>

<style lang="less" scoped>
@import "/src/assets/css/color.less";

#unlimited-plan-item {
  cursor: pointer;
  margin-bottom: 1em;

  &:hover {
    color: @blue;
  }

  > :first-child {
    margin-right: 1em;
  }
}
</style>
