import { getDataWithIdUrl, getDataWithoutIdUrl, getFilterUrl, addUrl, removeUrl, updateUrl } from '@/api/statistics'
import axios from 'axios'
import dayjs from 'dayjs'
import { removeElement, updateElement } from '@/utils/array'

const state = () => ({
  data: [],
  allData: [],
  filterTemplates: [],
  isTemplate: false,
  currentFilter: {
    name: '',
    names: [{ name: '' }],
    types: [{ isNegate: false, type: null }],
    devotions: [{ operator: null, rate: null }],
    satisfactions: [{ operator: null, rate: null }],
    times: [{ repeatType: 0, date: null, isNegate: false }]
  },
  currentTemplate: null,
  mode: 'data',
  currentData: null
})

const mutations = {
  initFilter (state, data) {
    console.log(data)
    state.filterTemplates.splice(0)
    state.filterTemplates.push(...data)
  },

  setData (state, taskArray) {
    state.data.splice(0)
    state.data.push(...taskArray)
    console.log(taskArray)
  },

  addFilter (state, filter) {
    state.filterTemplates.push(filter)
  },

  removeFilter (state, id) {
    removeElement(state.filterTemplates, id, 'id')
  },

  updateFilter (state, filter) {
    updateElement(state.filterTemplates, filter.id, filter, 'id')
  }
}

const actions = {
  getDataWithId ({ commit }, id) {
    axios.get(getDataWithIdUrl, {
      params: { id }
    })
      .then(res => {
        if (res.status === 200) {
          commit('setData', receiveData2Data(res.data))
        }
      })
  },

  getAllData ({ state }) {
    return new Promise(resolve => {
      axios.get(getDataWithIdUrl, {
        params: { id: 4 }
      })
        .then(res => {
          if (res.status === 200) {
            state.allData = receiveData2Data(res.data)
            resolve()
          }
        })
    })
  },

  getDataWithoutId ({ commit }, template) {
    return new Promise(resolve => {
      axios.post(getDataWithoutIdUrl, filter2PostFilter(template))
        .then(res => {
          if (res.status === 200) {
            commit('setData', receiveData2Data(res.data))
            resolve()
          }
        })
    })
  },

  initFilter ({ commit }) {
    axios(getFilterUrl)
      .then(res => {
        if (res.status === 200) {
          commit('initFilter', receiveFilters2Filters(res.data))
        }
      })
  },

  addFilter ({ commit }, rawFilter) {
    return new Promise(resolve => {
      const postFilter = filter2PostFilter(rawFilter)
      delete postFilter.id
      axios.post(addUrl, postFilter)
        .then(res => {
          if (res.status === 200) {
            commit('addFilter', { id: res.data, ...rawFilter })
            resolve()
          }
        })
    })
  },

  removeFilter ({ commit }, id) {
    return new Promise(resolve => {
      axios.delete(removeUrl + '/' + id)
        .then(res => {
          if (res.status === 200) {
            commit('removeFilter', id)
            resolve()
          }
        })
    })
  },

  updateFilter ({ commit }, filter) {
    return new Promise(resolve => {
      axios.post(updateUrl, filter2PostFilter(filter))
        .then(res => {
          if (res.status === 200) {
            commit('updateFilter', filter)
            resolve()
          }
        })
    })
  }
}

const receiveData2Data = (data) => {
  return data.map(item => {
    const tmp = {
      name: item.planByPlanId.planName,
      time: item.planByPlanId.time,
      taskType: item.planByPlanId.typeList,
      expectedTime: item.planByPlanId.expectedTime,
      startTime: dayjs(Number(item.startTime)),
      endTime: dayjs(Number(item.endTime)),
      devotion: item.planByPlanId.devotion,
      satisfaction: item.planByPlanId.satisfaction,
      note: item.planByPlanId.note,
      subTasks: item.planByPlanId.subTasks,
      track: item.planByPlanId.tracksById
    }
    tmp.track.forEach(item => {
      item.time = dayjs(Number(item.endTime)).diff(dayjs(Number(item.startTime)), 'minute')
    })
    return tmp
  })
}

const filter2PostFilter = (filter) => {
  console.log(filter)
  const result = {
    id: filter.id,
    templeteName: filter.name,
    stDevotionsById: [],
    stNamesById: [],
    stSatisfactionsById: [],
    stTimesById: [],
    stTypesById: []
  }

  for (const i in filter.names) {
    const temp = {}
    if (filter.names[i].name && filter.names[i].name.length > 0) {
      temp.name = filter.names[i].name
      result.stNamesById.push(temp)
    }
  }
  for (const i in filter.satisfactions) {
    const temp = {}
    if (filter.satisfactions[i].operator !== null && filter.satisfactions[i].rate !== null) {
      temp.operator = filter.satisfactions[i].operator
      temp.level = filter.satisfactions[i].rate
      result.stSatisfactionsById.push(temp)
    }
  }
  for (const i in filter.devotions) {
    const temp = {}
    if (filter.devotions[i].operator !== null && filter.devotions[i].rate !== null) {
      temp.operator = filter.devotions[i].operator
      temp.level = filter.devotions[i].rate
      result.stDevotionsById.push(temp)
    }
  }
  for (const i in filter.types) {
    const temp = {}
    if (
      filter.types[i].isNegate !== null &&
      filter.types[i].type &&
      filter.types[i].type.length
    ) {
      temp.notType = filter.types[i].isNegate
      temp.planType = filter.types[i].type[filter.types[i].type.length - 1]
      result.stTypesById.push(temp)
    }
  }
  for (const i in filter.times) {
    const temp = {}
    temp.notDate = filter.times[i].isNegate
    temp.repeatType = filter.times[i].repeatType
    if (filter.times[i].repeatType === 0) {
      if (
        filter.times[i].date &&
        filter.times[i].date.start &&
        filter.times[i].date.end
      ) {
        temp.startDate = filter.times[i].date.start.getTime().toString()
        temp.endDate = filter.times[i].date.end.getTime().toString()
      }
    } else if (
      filter.times[i].repeatType === 2 ||
      filter.times[i].repeatType === 3
    ) {
      temp.startDate = filter.times[i].date.reduce((result, cur, index) => {
        if (cur) {
          return result.concat((index + 1) + ',')
        }
        return result
      }, '').slice(0, -1)
    }
    if (temp.startDate) {
      result.stTimesById.push(temp)
    }
  }
  return result
}

const receiveFilters2Filters = (filters) => {
  return filters.map(filter => {
    const result = {
      id: filter.id,
      name: filter.templeteName,
      names: [],
      types: [],
      devotions: [],
      satisfactions: [],
      times: []
    }

    if (!filter.stDevotionsById.length) {
      result.devotions.push({ operator: null, rate: null })
    } else {
      for (const i in filter.stDevotionsById) {
        const temp = {}
        temp.operator = filter.stDevotionsById[i].operator
        temp.rate = filter.stDevotionsById[i].level
        result.devotions.push(temp)
      }
    }

    if (!filter.stNamesById.length) {
      result.names.push({ name: null })
    } else {
      for (const i in filter.stNamesById) {
        result.names[i] = { name: filter.stNamesById[i].name }
      }
    }

    if (!filter.stTypesById.length) {
      result.types.push({ isNegate: false, type: null })
    } else {
      for (const i in filter.stTypesById) {
        const temp = {}
        temp.isNegate = filter.stTypesById[i].notType
        temp.type = filter.stTypesById[i].typeList
        result.types.push(temp)
      }
    }

    if (!filter.stSatisfactionsById.length) {
      result.satisfactions.push({ operator: null, rate: null })
    } else {
      for (const i in filter.stSatisfactionsById) {
        const temp = {}
        temp.operator = filter.stSatisfactionsById[i].operator
        temp.rate = filter.stSatisfactionsById[i].level
        result.satisfactions.push(temp)
      }
    }

    if (!filter.stTimesById.length) {
      result.times.push({ repeatType: 0, date: null, isNegate: false })
    } else {
      for (const i in filter.stTimesById) {
        const temp = {}
        temp.isNegate = filter.stTimesById[i].notDate

        switch (filter.stTimesById[i].repeatType) {
          case 0:
            temp.date = {
              start: new Date(Number(filter.stTimesById[i].startDate)),
              end: new Date(Number(filter.stTimesById[i].endDate))
            }
            temp.repeatType = 0
            break
          case 1:
            temp.repeatType = 1
            temp.date = null
            break
          case 2:
            temp.repeatType = 2
            temp.date = filter.stTimesById[i].startDate.split(',').map(item => Number(item))
            break
          case 3:
            temp.repeatType = 3
            temp.date = filter.stTimesById[i].startDate.split(',').map(item => Number(item))
        }
        result.times.push(temp)
      }
    }
    return result
  })
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
