let taskTypeData

export function init (data) {
  taskTypeData = data
}

const getFullNameByNumber = (id) => {
  return id
}

const getFullNameByArray = (idArr) => {
  const nameArr = []
  let idPointer = 0

  function find (arr) {
    for (const e of arr) {
      if (e.id === idArr[idPointer]) {
        nameArr.push(e.name)
        idPointer++
        find(e.children)
        return
      }
    }
  }

  find(taskTypeData.children)
  return nameArr
}

export function getTaskTypeName (typeId) {
  let result

  function find (arr) {
    arr.forEach(node => {
      if (node.id === typeId) {
        result = node.name
        return
      }
      find(node.children)
    })
  }

  find(taskTypeData.children)
  return result
}

export function getTaskTypeFullNameArray (type) {
  if ((typeof type) === 'number') {
    return getFullNameByNumber(type)
  } else {
    return getFullNameByArray(type)
  }
}

const separator = ' > '
export function getTaskTypeFullName (type) {
  let fullName = ''
  const arr = getTaskTypeFullNameArray(type)
  for (let i = 0; i < arr.length - 1; i++) {
    fullName = fullName.concat(arr[i], separator)
  }
  fullName = fullName.concat(arr[arr.length - 1])
  return fullName
}

export function isVagueMatch (taskType, basedTaskType) {
  console.log(basedTaskType)
  for (let i = 0; i < basedTaskType.length; i++) {
    if (taskType[i] !== basedTaskType[i]) {
      return false
    }
  }
  return true
}
