<template>
  <div>
    <div v-if="isNull(data)">
      <el-button plain>
        无<slot name="name" />限制
      </el-button>
    </div>
    <div v-else>
      <el-badge :value="data.length">
        <el-button plain>
          <slot name="name" />：
          <slot name="text" />
        </el-button>
      </el-badge>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Item',
  props: {
    data: {
      type: Array,
      required: true
    },
    isNull: {
      type: Function,
      required: true
    }
  }
}
</script>

