export class Time {
  hour
  minute

  constructor (hour, minute) {
    this.hour = parseInt(hour)
    this.minute = parseInt(minute)
  }

  static compare (time1, time2) {
    if (time1.hour > time2.hour) { return 1 }
    if (time1.hour < time2.hour) { return -1 }
    if (time1.minute > time2.minute) { return 1 }
    if (time1.minute < time2.minute) { return -1 }
    return 0
  }

  toString () {
    return `${this.hour}:${this.minute}`
  }
}
