<template>
  <div id="task-main">
    <el-breadcrumb id="breadcrumb">
      <el-breadcrumb-item
        to="/task/start"
        replace
      >
        Start
      </el-breadcrumb-item>
      <el-breadcrumb-item
        v-if="$router.currentRoute.value.path==='/task/timing'||$router.currentRoute.value.path==='/task/finish'"
        to="/task/timing"
        replace
      >
        Timing
      </el-breadcrumb-item>
      <el-breadcrumb-item
        v-if="$router.currentRoute.value.path==='/task/finish'"
        replace
      >
        Finish
      </el-breadcrumb-item>
    </el-breadcrumb>
    <router-view v-slot="{ Component }">
      <keep-alive>
        <component :is="Component" />
      </keep-alive>
    </router-view>
  </div>
</template>

<script>
import { useRouter } from 'vue-router'

export default {
  name: 'Task',
  setup () {
    useRouter().push({
      name: 'Start'
    })
  }
}
</script>

<style lang="less" scoped>
#task-main{
  height: 100%;
  width: 100%;
  box-sizing: border-box;

  #breadcrumb{
    height: 2%;
    margin-bottom: 0.5em;
  }

  >:nth-child(2){
    height: 98%;
  }
}
</style>
