<template>
  <div id="task-panel">
    <el-form
      :model="state.currentTask"
      :rules="rules"
      ref="formEl"
    >
      <el-form-item
        label="任务类型"
        prop="taskType"
      >
        <TaskType v-model="state.currentTask.taskType" />
      </el-form-item>
      <el-form-item
        label="任务名称"
        prop="name"
      >
        <el-input
          placeholder="请输入"
          v-model="state.currentTask.name"
          clearable
        />
      </el-form-item>
      <el-form-item
        label="预期时间"
        prop="expectedTime"
      >
        <el-input-number
          v-model="state.currentTask.expectedTime"
          :disabled="state.currentTask.subTasks.length!==0"
          :min="0"
          :step="5"
        />
      </el-form-item>
    </el-form>

    <div id="sub-tasks">
      <h3>子任务</h3>
      <SubTasks
        :data="state.currentTask.subTasks"
        v-model:expected-time="state.currentTask.expectedTime"
      />
    </div>

    <el-button
      type="primary"
      @click="start"
      id="start-button"
    >
      开 始
    </el-button>
    <RulePopup
      v-model="showRulePopup"
      :data="rulePopupData"
    />
  </div>
</template>

<script>
import { ref } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import TaskType from '@/components/TaskType'
import SubTasks from '@/components/SubTasks'
import taskProxy from '@/proxy/taskProxy'
import RulePopup from '../RulePopup'
import { ElMessage } from 'element-plus'

export default {
  name: 'TaskPanel',
  components: {
    TaskType,
    SubTasks,
    RulePopup
  },
  setup () {
    const state = useStore().state.task
    const router = useRouter()
    taskProxy.init(useStore())

    const rulePopupData = ref({})
    const showRulePopup = ref(false)

    const formEl = ref(null)

    const start = () => {
      // 表单验证
      formEl.value.validate((valid) => {
        if (valid) {
          // 规则验证
          const validation = taskProxy.invoke(state.currentTask)
          if (typeof validation === 'object') {
            rulePopupData.value = validation
            showRulePopup.value = true
            return
          }
          router.push({
            name: 'Timing',
            params: {
              from: 'Start'
            }
          })
        } else {
          ElMessage({
            showClose: true,
            message: '请按要求输入!',
            type: 'error'
          })
          return false
        }
      })
    }

    const rules = {
      name: [
        { required: true, message: '请输入任务名称', trigger: 'blur' }
      ],
      taskType: [
        { required: true, message: '请输入任务类型', trigger: 'change' }
      ]
    }

    return {
      formEl,
      showRulePopup,
      rulePopupData,
      state,
      start,
      rules
    }
  }
}
</script>

<style lang="less" scoped>
#task-panel{
  display: flex;
  flex-direction: column;
  position: relative;

  .el-form{
    .el-form-item{
      display: flex;

      /deep/label{
        width: 6em;
      }
    }
  }

  #sub-tasks{
    h3{
      letter-spacing: 0.1em;
      margin-bottom: 0.5em;
    }
  }

  #start-button{
    width: 6em;
    align-self: center;
    position: absolute;
    bottom: 1em;
  }
}
</style>
