import axios from 'axios'
import {
  addTaskUrl, getTemplateUrl,
  addCustomTemplateUrl, removeCustomTemplateUrl, updateCustomTemplateUrl,
  addCommonTemplateUrl, removeCommonTemplateUrl, updateCommonTemplateUrl
} from '@/api/task'
import { removeElement, updateElement } from '@/utils/array'
import { reactive } from 'vue'

const state = () => ({
  common: [],
  custom: [],
  currentTask: reactive({
    name: '',
    taskType: [],
    expectedTime: 0,
    subTasks: [],
    time: 0,
    track: [],
    note: '',
    devotion: 3,
    satisfaction: 3,
    startTime: null,
    endTime: null
  }),
  currentTemplate: null,
  popupVisibility: {
    value: false,
    type: 0 // 0代表常用，1代表自定义
  },
  popupEditable: false
})

const mutations = {
  initCommon (state, common) {
    state.common.splice(0)
    state.common.push(...common)
  },

  initCustom (state, custom) {
    state.custom.splice(0)
    state.custom.push(...custom)
  },

  addCommonTemplate (state, template) {
    state.common.push(template)
  },

  addCustomTemplate (state, template) {
    state.custom.push(template)
  },

  removeCommonTemplate (state, id) {
    removeElement(state.common, id, 'id')
  },

  removeCustomTemplate (state, id) {
    removeElement(state.custom, id, 'id')
  },

  updateCommonTemplate (state, template) {
    updateElement(state.common, template.id, template, 'id')
  },

  updateCustomTemplate (state, template) {
    updateElement(state.custom, template.id, template, 'id')
  }
}

const actions = {
  init ({ commit }) {
    axios(getTemplateUrl)
      .then(res => {
        if (res.status === 200) {
          const common = res.data.commonTemplates.map(item => ({
            id: item.id,
            name: item.name,
            icon: item.icon,
            time: item.plan.time,
            taskType: item.plan.typeList,
            subTasks: item.plan.subTasks.map(item => {
              item._id = item.id
              return item
            })
          }))
          const custom = res.data.customTemplates.map(item => ({
            id: item.id,
            name: item.name,
            icon: item.icon,
            time: item.plan.time,
            taskType: item.plan.typeList,
            subTasks: item.plan.subTasks.map(item => {
              item._id = item.id
              return item
            })
          }))
          commit('initCommon', common)
          commit('initCustom', custom)
        }
      })
  },

  addTask ({ commit }, task) {
    return new Promise(resolve => {
      const postTask = {
        ...task,
        planName: task.name,
        startTime: String(task.startTime.getTime()),
        endTime: String(task.endTime.getTime()),
        type: task.taskType[task.taskType.length - 1]
      }
      postTask.subTasks.forEach(item => {
        delete item.id
      })
      postTask.tracks = postTask.track.map(item => ({
        name: item.name,
        time: item.time,
        startTime: String(item.startTime.getTime()),
        endTime: String(item.endTime.getTime())
      }))
      delete postTask.id
      axios.post(addTaskUrl, postTask)
        .then(res => {
          if (res.status === 200) {
            resolve()
          }
        })
    })
  },

  addCommonTemplate ({ commit }, rawTemplate) {
    const postTemplate = {
      icon: rawTemplate.icon,
      name: rawTemplate.name,
      plan: {
        ...rawTemplate,
        taskType: { id: rawTemplate.taskType[rawTemplate.taskType.length - 1] }
      }
    }
    axios.post(addCommonTemplateUrl, postTemplate)
      .then(res => {
        if (res.status === 200) {
          commit('addCommonTemplate', { id: res.data, ...rawTemplate })
        }
      })
  },

  addCustomTemplate ({ commit }, rawTemplate) {
    const postTemplate = {
      icon: rawTemplate.icon,
      name: rawTemplate.name,
      plan: {
        ...rawTemplate,
        taskType: { id: rawTemplate.taskType[rawTemplate.taskType.length - 1] }
      }
    }
    axios.post(addCustomTemplateUrl, postTemplate)
      .then(res => {
        if (res.status === 200) {
          commit('addCustomTemplate', { id: res.data, ...rawTemplate })
        }
      })
  },

  removeCommonTemplate ({ commit }, id) {
    return new Promise(resolve => {
      axios.post(removeCommonTemplateUrl, { id })
        .then(res => {
          if (res.status === 200) {
            commit('removeCommonTemplate', id)
            resolve()
          }
        })
    })
  },

  removeCustomTemplate ({ commit }, id) {
    return new Promise(resolve => {
      axios.post(removeCustomTemplateUrl, { id })
        .then(res => {
          if (res.status === 200) {
            commit('removeCustomTemplate', id)
            resolve()
          }
        })
    })
  },

  updateCommonTemplate ({ commit }, template) {
    const postTemplate = {
      id: template.id,
      icon: template.icon,
      name: template.name,
      plan: {
        ...template,
        taskType: { id: template.taskType[template.taskType.length - 1] }
      }
    }
    axios.post(updateCommonTemplateUrl, postTemplate)
      .then(res => {
        if (res.status === 200) {
          commit('updateCommonTemplate', template)
        }
      })
  },

  updateCustomTemplate ({ commit }, template) {
    const postTemplate = {
      id: template.id,
      icon: template.icon,
      name: template.name,
      plan: {
        ...template,
        taskType: { id: template.taskType[template.taskType.length - 1] }
      }
    }
    axios.post(updateCustomTemplateUrl, postTemplate)
      .then(res => {
        if (res.status === 200) {
          commit('updateCustomTemplate', template)
        }
      })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
