<template>
  <div
    id="track"
    :class="size"
  >
    <el-timeline>
      <el-timeline-item
        v-for="(item, index) in data"
        :key="index"
        hide-timestamp
        size="large"
      >
        <div class="track-item">
          <span>{{ item.name }}</span>
          <span>进行 {{ item.time }} 分钟</span>
          <span>{{ formatTime(item.startTime) }} - {{ formatTime(item.endTime) }}</span>
        </div>
      </el-timeline-item>
    </el-timeline>
  </div>
</template>

<script>
import TimeManager from '@/utils/time'
import dayjs from 'dayjs'

export default {
  name: 'Track',
  props: {
    data: {
      type: Array,
      required: true
    },
    size: {
      type: String,
      default: 'default'
    }
  },
  setup () {
    const formatTime = (time) => {
      time = dayjs(Number(time))
      return time.format('HH:mm')
    }

    return {
      formatTime,
      getMinute: TimeManager.second2Minute
    }
  }
}
</script>

<style lang="less" scoped>
.large{
  span{
    font-size: large;
    letter-spacing: 0.1em;
  }
}

.default{
  span{
    font-size: 16px;
  }
}

.track-item{
  >span:nth-of-type(1){
    margin-right: 2em;
  }
  >span:nth-of-type(2){
    margin-right: 2em;
  }
}
</style>
