<template>
  <div id="process-circle">
    <div ref="processChart" />
    <div
      v-show="!working"
      id="rest-mask"
    >
      <div>休息中...</div>
      <div>{{ time }}</div>
    </div>
  </div>
</template>

<script>
import { onMounted, ref, watch } from 'vue'
import { init } from 'echarts'

export default {
  name: 'ProcessCircle',
  props: {
    time: {
      type: Number,
      required: true
    },
    tasks: {
      type: Array,
      required: true
    },
    currentTaskIndex: {
      type: Number,
      required: true
    },
    percentage: {
      type: Number,
      required: true
    },
    working: {
      type: Boolean,
      required: true
    }
  },
  setup (props) {
    const processChart = ref(null)
    let chart

    const option = {
      series: [{
        type: 'gauge',
        startAngle: 90,
        endAngle: -270,
        radius: '95%',
        pointer: {
          show: false
        },
        progress: {
          show: true,
          overlap: false,
          roundCap: true,
          clip: false
        },
        axisLine: {
          lineStyle: {
            width: 40
          }
        },
        splitLine: {
          show: false,
          distance: 0,
          length: 10
        },
        axisTick: {
          show: false
        },
        axisLabel: {
          show: false,
          distance: 50
        },
        data: [],
        title: {
          fontSize: 14
        },
        detail: {
          width: 50,
          height: 14,
          fontSize: 14,
          color: 'inherit',
          borderColor: 'inherit',
          borderRadius: 20,
          borderWidth: 1,
          formatter: (val) => {
            return Math.floor(val) + '%'
          }
        }
      }]
    }

    const iniOptionData = () => {
      const tasks = props.tasks
      const data = []
      const middle = Math.floor(tasks.length / 2)
      const innerInterval = 10
      const betweenInterval = 20
      for (let i = 0; i < tasks.length; i++) {
        const item = tasks[i]
        data.push({
          value: 0,
          name: item.name,
          title: {
            offsetCenter: ['0%', (i - middle) * (innerInterval + betweenInterval) + '%']
          },
          detail: {
            offsetCenter: ['0%', (innerInterval + (i - middle) * (innerInterval + betweenInterval)) + '%']
          }
        })
      }
      return data
    }

    watch([() => props.working, () => props.currentTaskIndex], () => {
      if (props.working) {
        option.series[0].data[props.currentTaskIndex].value = props.percentage
        chart.setOption(option, true)
      }
    })

    watch(() => props.percentage, () => {
      option.series[0].data[props.currentTaskIndex].value = props.percentage
      chart.setOption(option, true)
    })

    const draw = () => {
      option.series[0].data = iniOptionData()
      chart.setOption(option, true)
    }

    onMounted(() => {
      chart = init(processChart.value)
      draw()
    })

    return {
      processChart,
      draw
    }
  }
}
</script>

<style lang="less" scoped>
#process-circle{
  width: 100%;
  height: 70%;
  position: relative;

  &>:first-child{
    width: 100%;
    height: 100%;
  }

  #rest-mask{
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    flex-direction: column;
    top: 0;
    left: 0;
    align-items: center;
    justify-content: center;
    backdrop-filter: blur(4px) grayscale(0.5);

    &>:nth-child(1){
      font-size: 3em;
    }

    &>:nth-child(2){
      font-size: 2.5em;
    }
  }
}
</style>
