<template>
  <el-tabs v-model="activeTab">
    <el-tab-pane
      label="子任务"
      name="0"
    >
      <div v-if="state.currentData===null">
        无
      </div>
      <ReadonlySubTasks
        v-else
        :data="state.currentData.subTasks"
      />
    </el-tab-pane>
    <el-tab-pane
      label="时间线"
      name="1"
    >
      <div v-if="state.currentData===null">
        无
      </div>
      <Track
        v-else
        :data="state.currentData.track"
        id="track"
      />
    </el-tab-pane>
    <el-tab-pane
      label="笔 记"
      name="2"
    >
      <div v-if="state.currentData===null || state.currentData.note===''">
        无
      </div>
      <div v-else>
        {{ state.currentData.note }}
      </div>
    </el-tab-pane>
  </el-tabs>
</template>

<script>
import { ref } from 'vue'
import { useStore } from 'vuex'
import ReadonlySubTasks from '@/components/ReadonlySubTasks'
import Track from '@/components/Track'

export default {
  name: 'Detail',
  components: {
    ReadonlySubTasks,
    Track
  },
  setup () {
    const store = useStore()
    const state = store.state.statistics
    const activeTab = ref('0')

    return {
      state,
      activeTab
    }
  }
}
</script>

<style lang="less" scoped>
#track{
  margin-left: 1em;
}
</style>
