<template>
  <div id="current-filter">
    <el-popover
      placement="bottom-start"
      trigger="manual"
      v-model:visible="popup"
    >
      <template #reference>
        <div
          id="items"
          @click="popup=!popup"
        >
          <Item
            :data="state.currentFilter.times"
            :is-null="isTimesNull"
          >
            <template #name>
              时间
            </template>
            <template #text>
              {{ formatDate(state.currentFilter.times[0]) }}
            </template>
          </Item>
          <Item
            :data="state.currentFilter.names"
            :is-null="isNamesNull"
          >
            <template #name>
              名称
            </template>
            <template #text>
              {{ state.currentFilter.names[0].name }}
            </template>
          </Item>
          <Item
            :data="state.currentFilter.types"
            :is-null="isTypesNull"
          >
            <template #name>
              类型
            </template>
            <template #text>
              {{ getTaskTypeFullName(state.currentFilter.types[0].type) }}
            </template>
          </Item>
          <Item
            :data="state.currentFilter.devotions"
            :is-null="isRatesNull"
          >
            <template #name>
              投入度
            </template>
            <template #text>
              {{ OPERATOR_OPTIONS[state.currentFilter.devotions[0].operator].label }} {{ state.currentFilter.devotions[0].rate }}
            </template>
          </Item>
          <Item
            :data="state.currentFilter.satisfactions"
            :is-null="isRatesNull"
          >
            <template #name>
              满意度
            </template>
            <template #text>
              {{ OPERATOR_OPTIONS[state.currentFilter.satisfactions[0].operator].label }} {{ state.currentFilter.satisfactions[0].rate }}
            </template>
          </Item>
        </div>
      </template>
      <Filter />
    </el-popover>
  </div>
</template>

<script>
import { ref } from 'vue'
import { useStore } from 'vuex'
import Filter from '../Filter/Index'
import Item from './Item'
import { getTaskTypeFullName } from '@/utils/taskType'
import { OPERATOR_OPTIONS } from '@/interface/enum'
import dayjs from 'dayjs'
import _ from 'lodash'

export default {
  name: 'CurrentFilter',
  components: {
    Filter,
    Item
  },
  setup () {
    const store = useStore()
    const state = store.state.statistics

    const popup = ref(false)

    const isNamesNull = (names) => {
      if (names && names.length) {
        for (const name of names) {
          if (name.name) {
            return false
          }
        }
        return true
      }
      return true
    }

    const isTypesNull = (types) => {
      if (types && types.length) {
        for (const type of types) {
          if (type.type) {
            return false
          }
        }
        return true
      }
      return true
    }

    const isRatesNull = (rates) => {
      if (rates && rates.length) {
        for (const rate of rates) {
          if (rate.rate !== null && rate.operator !== null) {
            return false
          }
        }
        return true
      }
      return true
    }

    const isTimesNull = (times) => {
      if (times && times.length) {
        for (const time of times) {
          switch (time.repeatType) {
            case 0: // 具体日期
              if (time.date && time.date.start) {
                return false
              }
              break
            case 1: // 每天
              return false
            case 2: // 每周
            case 3: // 每月
              if (time.date && length in time.date) {
                for (const e of time.date) {
                  if (e) {
                    return false
                  }
                }
              }
              break
          }
        }
        return true
      }
      return true
    }

    const weekValue = ['一', '二', '三', '四', '五', '六', '日']
    const weekDay = _.fill(Array(7), false)
    const monthDay = _.fill(Array(31), false) // [2, 5]->[]
    const formatDate = (date) => {
      switch (date.repeatType) {
        case 0: // 具体日期
          return dayjs(date.date.start).format('YYYY/MM/DD') + ' - ' + dayjs(date.date.end).format('YYYY/MM/DD')
        case 1: // 每天
          return '每天'
        case 2: // 每周
          date.date.forEach(item => {
            weekDay[item - 1] = true
          })
          return weekDay.reduce((str, cur, index) => {
            if (cur) {
              return str.concat(weekValue[index] + '、')
            }
            return str
          }, '').slice(0, -1)
          // return date.date.reduce((str, cur, index) => {
          //   if (cur) {
          //     return str.concat(weekValue[index] + '、')
          //   }
          //   return str
          // }, '').slice(0, -1)
        case 3:// 每月
          date.date.forEach(item => {
            monthDay[item - 1] = true
          })
          return monthDay.reduce((str, cur, index) => {
            if (cur) {
              return str.concat(index + ',')
            }
            return str
          }, '').slice(0, -2)
          // return date.date.reduce((str, cur, index) => {
          //   if (cur) {
          //     return str.concat(index + ', ')
          //   }
          //   return str
          // }, '').slice(0, -2)
      }
    }

    return {
      state,
      popup,
      isNamesNull,
      isTypesNull,
      isRatesNull,
      isTimesNull,
      getTaskTypeFullName,
      OPERATOR_OPTIONS,
      formatDate
    }
  }
}
</script>

<style lang="less" scoped>
#current-filter{
  display: flex;
  align-items: center;
  width: 71%;
  padding: 0;

  #items{
    display: flex;
    align-items: center;

    >*{
      margin-right: 1em;
    }
  }
}
</style>
