<template>
  <el-select
    v-model="templateId"
    @change="onChange"
    placeholder="过滤模板"
  >
    <el-option
      v-for="item in data"
      :key="item.id"
      :value="item.id"
      :label="item.name"
    />
  </el-select>
</template>

<script>
import { ref, watch } from 'vue'
import { useStore } from 'vuex'
import _ from 'lodash'

export default {
  name: 'FilterTemplate',
  components: {
  },
  setup () {
    const state = useStore().state.statistics
    const data = state.filterTemplates

    const templateId = ref(null)

    const onChange = () => {
      state.isTemplate = true
      const index = _.findIndex(data, item => item.id === templateId.value)
      state.currentTemplate = data[index]
    }

    watch(() => state.isTemplate, () => {
      if (!state.isTemplate) {
        templateId.value = null
      }
    })

    return {
      state,
      data,
      onChange,
      templateId
    }
  }
}
</script>

