<template>
  <el-dialog
    :title="`违反${formatTitle(data.rule)}规则`"
    v-model="visibility"
    width="30%"
  >
    <div>
      禁止 {{ getTaskTypeFullName(data.taskType) }} 类任务
    </div>
    <div v-if="data.rule==='time'">
      在 {{ data.startTime.toString() }} - {{ data.endTime.toString() }} 之间进行
    </div>
    <div v-if="data.rule==='frequency'">
      {{ formatTime(data.time) }}超过{{ data.frequency }}次
    </div>
    <div v-if="data.rule==='order'">
      在 {{ getTaskTypeFullName(data.basedTaskType) }}类任务之{{ formatOrder(data.order) }}进行
    </div>
    <template #footer>
      <span class="dialog-footer">
        <el-button
          type="primary"
          @click="visibility = false"
        >确 定</el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import { getTaskTypeFullName } from '@/utils/taskType'
import { VModel } from '@/utils'

export default {
  name: 'RulePopup',
  props: {
    modelValue: {
      type: Boolean,
      default: false
    },
    data: {
      type: Object,
      required: true
    }
  },
  emits: ['update:modelValue'],
  setup (props, ctx) {
    const visibility = VModel(props, ctx)
    console.log(props.data)
    const formatTitle = (type) => {
      switch (type) {
        case 'time':
          return '时间'
        case 'frequency':
          return '次数'
        case 'order':
          return '顺序'
      }
    }

    const formatTime = (time) => {
      switch (time) {
        case 1:
          return '每天'
        case 2:
          return '每周'
        case 3:
          return '每月'
      }
    }

    const formatOrder = (order) => {
      if (order === 0) {
        return '前'
      } else {
        return '后'
      }
    }

    return {
      visibility,
      formatTitle,
      formatTime,
      formatOrder,
      getTaskTypeFullName
    }
  }
}
</script>

