<template>
  <main>
    <div
      v-once
      id="title"
    >
      {{ title }}
    </div>
    <div id="content">
      <div
        v-for="(item, index) in filterData"
        :key="item"
      >
        <Template
          :name="name"
          :data="item"
          @remove="remove(index)"
        />
      </div>
    </div>
    <div class="button-container">
      <el-button
        @click="add"
        icon="el-icon-plus"
        size="mini"
      />
    </div>
  </main>
</template>

<script>
import Template from './Template'
import { ref, watch } from 'vue'

export default {
  name: 'TemplateFilterGroup',
  components: {
    Template
  },
  props: {
    title: {
      type: String,
      required: true
    },
    name: {
      type: String,
      required: true
    },
    data: {
      type: Array,
      default: () => { return [{}] }
    }
  },
  setup (props) {
    const filterData = ref(props.data)

    watch(() => props.data, () => {
      filterData.value = props.data
    })

    let createNullData
    switch (props.name) {
      case 'Time':
        createNullData = () => {
          return {
            date: null,
            isNegate: false,
            repeatType: 0
          }
        }
        break
      case 'Type':
        createNullData = () => {
          return {
            isNegate: false,
            type: null
          }
        }
        break
      case 'Name':
        createNullData = () => {
          return {
            name: null
          }
        }
        break
      case 'Rate':
        createNullData = () => {
          return {
            operator: null,
            rate: null
          }
        }
        break
      default:
        createNullData = () => {}
    }

    const add = () => {
      filterData.value.push(createNullData())
    }
    const remove = (index) => {
      filterData.value.splice(index, 1)
      if (filterData.value.length === 0) {
        add()
      }
    }

    return {
      filterData,
      add,
      remove
    }
  }
}
</script>

<style lang="less" scoped>
main{
  display: flex;
  flex-direction: row;
}

#title{
  margin: 0 0.5em;
  width: 3.5em;
}

#content{
  display: flex;
  flex-direction: column;
}

.button-container{
  display: flex;
  flex-direction: column-reverse;
  margin-left: auto;
}
</style>
