<template>
  <div>
    <div
      v-if="type===-1"
      id="null"
    >
      未选择
    </div>
    <!-- 时间限制计划 -->
    <div
      v-else-if="type===0"
      class="plan-details"
    >
      <h3>计划详情</h3>
      <div>
        <span>名称</span>
        <span>{{ planDetails.get().name }}</span>
      </div>
      <div>
        <span>开始时间</span>
        <span>{{ showTime(planDetails.get().startTime[0]) }}</span>
        <span>-</span>
        <span>{{ showTime(planDetails.get().startTime[1]) }}</span>
      </div>
      <div>
        <span>结束时间</span>
        <span>{{ showTime(planDetails.get().endTime[0]) }}</span>
        <span>-</span>
        <span>{{ showTime(planDetails.get().endTime[1]) }}</span>
      </div>
      <div>
        <span>类型</span>
        <span>{{ getTaskTypeFullName(planDetails.get().taskType) }}</span>
      </div>
      <div class="sub-tasks">
        <span>子任务</span>
        <div>
          <span
            v-for="subTask in planDetails.get().subTasks"
            :key="subTask"
          >
            <span style="margin-right: 1em;">{{ subTask.name }}</span>
            <span>预期时间 {{ subTask.expectedTime }} 分钟</span>
          </span>
        </div>
      </div>
      <el-button-group>
        <el-button
          @click="showAddOrderLimitedPlanPopup=true"
          size="mini"
        >
          添加计划
        </el-button>
        <el-button
          @click="showAddRelationPopup=true"
          size="mini"
        >
          添加限制
        </el-button>
        <el-button
          @click="showRemovePlanPopup=true"
          size="mini"
        >
          删除计划
        </el-button>
        <el-button
          @click="showUpdateTimeLimitedPlanPopup=true"
          size="mini"
        >
          修改计划
        </el-button>
      </el-button-group>
    </div>
    <!-- 顺序限制计划 -->
    <div
      v-else-if="type===1"
      class="plan-details"
    >
      <h3>计划详情</h3>
      <div>
        <span>名称</span>
        <span>{{ planDetails.get().name }}</span>
      </div>
      <div>
        <span>类型</span>
        <span>{{ getTaskTypeFullName(planDetails.get().taskType) }}</span>
      </div>
      <div class="sub-tasks">
        <span>子任务</span>
        <div>
          <span
            v-for="subTask in planDetails.get().subTasks"
            :key="subTask"
          >
            <span style="margin-right: 1em;">{{ subTask.name }}</span>
            <span>预期时间 {{ subTask.expectedTime }} 分钟</span>
          </span>
        </div>
      </div>
      <el-button-group>
        <el-button
          @click="showAddOrderLimitedPlanPopup=true"
          size="mini"
        >
          添加计划
        </el-button>
        <el-button
          @click="showAddRelationPopup=true"
          size="mini"
        >
          添加限制
        </el-button>
        <el-button
          @click="showRemovePlanPopup=true"
          size="mini"
        >
          删除计划
        </el-button>
        <el-button
          @click="showUpdateOrderLimitedPlanPopup=true"
          size="mini"
        >
          修改计划
        </el-button>
      </el-button-group>
    </div>
    <!-- 无限制计划 -->
    <div
      v-else-if="type===2"
      id="unlimited-plan"
    >
      <div>
        <span>类型</span>
        <span>{{ getTaskTypeFullName(planDetails.get().typeList) }}</span>
      </div>
      <div>
        <span>名称</span>
        <span>{{ planDetails.get().name }}</span>
      </div>
      <div class="sub-tasks">
        <span>子任务</span>
        <span style="display: flex; flex-direction: column">
          <span
            v-for="subTask in planDetails.get().subTasks"
            :key="subTask"
            style="margin-bottom: 1em;"
          >
            <span style="margin-right: 0.5em">{{ subTask.name }}</span>
            <span> 预期{{ subTask.expectedTime }} 分钟</span>
          </span>
        </span>
      </div>
    </div>
    <!-- 关系 -->
    <div
      v-else-if="type===3"
      class="plan-details"
    >
      <h3>关系</h3>
      <div>
        <span>起点计划</span>
        <span>{{ getPlanNameById(planDetails.get().fromId) }}</span>
      </div>
      <div>
        <span>终点计划</span>
        <span>{{ getPlanNameById(planDetails.get().toId) }}</span>
      </div>
      <el-button-group>
        <el-button
          @click="showRemoveRelationPopup=true"
          size="mini"
        >
          删除限制
        </el-button>
      </el-button-group>
    </div>
    <!--  日期计划  -->
    <div
      v-else-if="type===4"
      class="plan-details"
    >
      <div>
        <span>开始时间</span>
        <span>{{ formatDate(planDetails.get().startDate) }}</span>
      </div>
      <div>
        <span>结束时间</span>
        <span>{{ formatDate(planDetails.get().endDate) }}</span>
      </div>
      <div class="sub-tasks">
        <span>子任务</span>
        <div>
          <span
            v-for="subTask in planDetails.get().subTasks"
            :key="subTask"
          >
            <span style="margin-right: 1em;">{{ subTask.name }}</span>
            <span>预期时间 {{ subTask.expectedTime }} 分钟</span>
          </span>
        </div>
      </div>
      <el-button-group>
        <el-button
          @click="showRemovePlanPopup=true"
          size="mini"
        >
          删除计划
        </el-button>
      </el-button-group>
    </div>
    <AddOrderLimitedPlanPopup v-model="showAddOrderLimitedPlanPopup" />
    <AddTimeLimitedPlanPopup v-model="showAddTimeLimitedPlanPopup" />
    <AddRelationPopup v-model="showAddRelationPopup" />
    <RemovePlanPopup v-model="showRemovePlanPopup" />
    <RemoveRelationPopup v-model="showRemoveRelationPopup" />
    <UpdateOrderLimitedPlanPopup v-model="showUpdateOrderLimitedPlanPopup" />
    <UpdateTimeLimitedPlanPopup v-model="showUpdateTimeLimitedPlanPopup" />
  </div>
</template>

<script>
import { ref } from 'vue'
import { useStore } from 'vuex'
import planDetails from '@/store/planDetails'
import AddOrderLimitedPlanPopup from '../LimitedPlan/popup/AddOrderLimitedPlanPopup'
import AddTimeLimitedPlanPopup from '@/views/Plan/LimitedPlan/popup/AddTimeLimitedPlanPopup'
import AddRelationPopup from '../LimitedPlan/popup/AddRelationPopup'
import RemovePlanPopup from '../LimitedPlan/popup/RemovePlanPopup'
import RemoveRelationPopup from '../LimitedPlan/popup/RemoveRelationPopup'
import UpdateOrderLimitedPlanPopup from '../LimitedPlan/popup/UpdateOrderLimitedPlanPopup'
import UpdateTimeLimitedPlanPopup from '@/views/Plan/LimitedPlan/popup/UpdateTimeLimitedPlanPopup'
import TimeManager from '@/utils/time'
import { getTaskTypeFullName } from '@/utils/taskType'
import dayjs from 'dayjs'

export default {
  name: 'PlanDetails',
  components: {
    AddOrderLimitedPlanPopup,
    AddTimeLimitedPlanPopup,
    AddRelationPopup,
    RemovePlanPopup,
    RemoveRelationPopup,
    UpdateOrderLimitedPlanPopup,
    UpdateTimeLimitedPlanPopup
  },
  setup () {
    const state = useStore().state.dateTimePlan

    const showAddOrderLimitedPlanPopup = ref(false)
    const showAddTimeLimitedPlanPopup = ref(false)
    const showAddRelationPopup = ref(false)
    const showRemovePlanPopup = ref(false)
    const showRemoveRelationPopup = ref(false)
    const showUpdateOrderLimitedPlanPopup = ref(false)
    const showUpdateTimeLimitedPlanPopup = ref(false)

    const type = ref(-1)
    const updateType = (id) => {
      if (typeof id === 'number') { // 无限制计划
        type.value = 2
        return
      }
      switch (id[0]) {
        case 't': // 时间限制计划
          type.value = 0
          return
        case 'o': // 顺序限制计划
          type.value = 1
          return
        case 'r': // 关系
          type.value = 3
          return
        case 'w': // 周、月计划
          type.value = 4
          return
        default: // 默认值
          type.value = -1
      }
    }
    planDetails.onChange = id => {
      updateType(id)
    }

    const showTime = (time) => {
      if (time) {
        return TimeManager.timestamp2time(time)
      } else {
        return '无'
      }
    }

    const getPlanNameById = (id) => {
      console.log(id)
      let planSet
      if (id[0] === 'o') {
        planSet = state.orderLimitedPlanSet
      } else {
        planSet = state.timeLimitedPlanSet
      }
      for (const plan of planSet) {
        if (plan.id === id) {
          return plan.name
        }
      }
    }

    const formatDate = (date) => {
      return dayjs(date).format('YYYY/MM/DD')
    }

    return {
      type,
      planDetails,
      showAddOrderLimitedPlanPopup,
      showAddTimeLimitedPlanPopup,
      showAddRelationPopup,
      showRemovePlanPopup,
      showRemoveRelationPopup,
      showUpdateOrderLimitedPlanPopup,
      showUpdateTimeLimitedPlanPopup,
      showTime,
      getTaskTypeFullName,
      getPlanNameById,
      formatDate
    }
  }
}
</script>

<style lang="less" scoped>
@import "/src/assets/css/color.less";

#null{
  font-size: 2em;
  color: @black4;
  display: flex;
  justify-content: center;
  letter-spacing: 0.2em;
  width: 100%;
  height: 100%;
}

.plan-details{
  display: flex;
  flex-direction: column;
  width: 100%;

  h3{
    letter-spacing: 0.1em;
    margin-bottom: 0.5em;
  }

  >div{
    display: flex;
    margin-bottom: 0.5em;

    >span{
      margin-right: 0.5em;
    }

    >span:first-child{
      width: 5em;
      margin-right: 0;
    }
  }

  .el-button-group{
    margin-top: auto;
    margin-left: auto;
    margin-right: auto;
  }
}

#unlimited-plan{
  >div{
    display: flex;
    margin-bottom: 1em;

    >span:nth-of-type(1){
      margin-right: 1em;
      width: 4em;
    }
  }
}

.sub-tasks{
  display: flex;
  margin-bottom: 1em;

  >span:nth-of-type(1){
    margin-right: 1em;
    width: 4em;
  }

  >div{
    display: flex;
    flex-direction: column;

    >*{
      margin-bottom: 0.5em;
    }
  }
}
</style>
