<template>
  <el-table
    :data="state.data"
    :default-sort="{ prop: 'date', order: 'descending' }"
    @row-click="onClickRow"
    height="917"
    stripe
  >
    <el-table-column
      v-for="item in columnOption"
      :key="item.prop"
      :prop="item.prop"
      :label="item.label"
      :formatter="item.formatter"
      :sortable="true"
    />
  </el-table>
</template>

<script>
import { useStore } from 'vuex'
import { getTaskTypeFullName } from '@/utils/taskType'

export default {
  name: 'List',
  setup () {
    const store = useStore()
    const state = store.state.statistics

    const columnOption = [{
      prop: 'name',
      label: '名称',
      formatter: null
    }, {
      prop: 'taskType',
      label: '类型',
      formatter: (row, column, cellValue, index) => {
        return getTaskTypeFullName(cellValue)
      }
    }, {
      prop: 'time',
      label: '有效时间',
      formatter: (row, column, cellValue, index) => {
        return cellValue
      }
    }, {
      prop: 'expectedTime',
      label: '预期时间',
      formatter: (row, column, cellValue, index) => {
        if (cellValue === null) {
          return '无'
        } else {
          return cellValue
        }
      }
    }, {
      prop: 'startTime',
      label: '开始时间',
      formatter: (row, column, cellValue, index) => {
        return cellValue.format('YYYY/MM/DD HH:mm')
      }
    }, {
      prop: 'endTime',
      label: '结束时间',
      formatter: (row, column, cellValue, index) => {
        return cellValue.format('YYYY/MM/DD HH:mm')
      }
    }, {
      prop: 'satisfaction',
      label: '满意度',
      formatter: null
    }, {
      prop: 'devotion',
      label: '投入度',
      formatter: null
    }]

    const onClickRow = (data) => {
      state.currentData = data
    }

    return {
      state,
      columnOption,
      onClickRow
    }
  }
}
</script>

<style lang="less" scoped>

</style>
