<template>
  <el-dialog
    title="添加时间限制计划"
    v-model="show"
    width="35%"
  >
    <el-form
      :model="data"
      :rules="rules"
    >
      <el-form-item
        label="名称"
        prop="name"
      >
        <el-input
          v-model="data.name"
          style="width: 20em;"
          size="medium"
          clearable
        />
      </el-form-item>
      <el-form-item
        label="计划类型"
        prop="taskType"
      >
        <TaskType
          v-model="data.taskType"
          size="medium"
        />
      </el-form-item>
      <el-form-item
        label="开始时间"
        prop="startTime"
      >
        <el-time-picker
          v-model="data.startTime[0]"
          @change="onStartTimeChange"
          format="HH:mm"
          placeholder="无"
          size="medium"
        />
        <el-time-picker
          v-model="data.startTime[1]"
          @change="onStartTimeChange"
          format="HH:mm"
          placeholder="无"
          size="medium"
        />
        <span>{{ showTimeOrder(startTimeOrder, '开始') }}</span>
      </el-form-item>
      <el-form-item
        label="结束时间"
        prop="endTime"
      >
        <el-time-picker
          v-model="data.endTime[0]"
          @change="onEndTimeChange"
          format="HH:mm"
          placeholder="无"
          size="medium"
        />
        <el-time-picker
          v-model="data.endTime[1]"
          @change="onEndTimeChange"
          format="HH:mm"
          placeholder="无"
          size="medium"
        />
        <span>{{ showTimeOrder(endTimeOrder, '完成') }}</span>
      </el-form-item>
      <el-form-item
        label="持续时间"
        prop="time"
      >
        <el-input-number
          v-model="data.time"
          :min="0"
          :step="5"
          size="medium"
          :disabled="data.subTasks.length!==0"
          class="input-expected-time"
        />
        <span>分钟</span>
      </el-form-item>
      <el-form-item
        label="子任务"
        prop="subTasks"
      >
        <SubTasks
          :data="data.subTasks"
          v-model:expected-time="data.time"
        />
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="show = false">取 消</el-button>
        <el-button
          type="primary"
          @click="add"
        >确 定</el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import { reactive, ref } from 'vue'
import { VModel } from '@/utils'
import { planHandleProxy } from '../TimeView/planProxy'
import TaskType from '@/components/TaskType'
import SubTasks from '@/components/SubTasks'
import dayjs from 'dayjs'

export default {
  name: 'AddTimeLimitedPlanPopup',
  components: {
    TaskType,
    SubTasks
  },
  props: {
    modelValue: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue'],
  setup (props, ctx) {
    const show = VModel(props, ctx)

    const data = reactive({
      startTime: [null, null],
      endTime: [null, null],
      time: 0,
      name: '',
      taskType: [],
      subTasks: []
    })

    const startTimeOrder = ref(0)
    const endTimeOrder = ref(0)

    const onStartTimeChange = () => {
      setTimeOrder(data.startTime, startTimeOrder)
    }

    const onEndTimeChange = () => {
      setTimeOrder(data.endTime, endTimeOrder)
    }

    const setTimeOrder = (time, timeOrder) => {
      if (data.time[0] !== null) {
        if (data.time[1] !== null) {
          timeOrder.value = 3
        } else {
          timeOrder.value = 2
        }
      } else {
        if (data.time[1] !== null) {
          timeOrder.value = 1
        } else {
          timeOrder.value = 0
        }
      }
    }

    const showTimeOrder = (order, postfix) => {
      switch (order) {
        case 1:
          return '之前' + postfix
        case 2:
          return '之后' + postfix
        case 3:
          return '之间' + postfix
        default:
          return ''
      }
    }

    const add = () => {
      planHandleProxy.addTimeLimitedPlan(data)
      show.value = false
    }

    const validateTime = (rule, value, cb) => {
      if (value[0] && value[1]) {
        if (dayjs(value[0]).isBefore(dayjs(value[1]))) {
          cb()
        } else {
          cb(new Error('不是正确的时间区间！'))
        }
      } else {
        cb()
      }
    }

    const validateStartTime = (rule, value, cb) => {
      if (value[0] || value[1]) {
        if (!data.endTime[0] && !data.endTime[1]) {
          cb()
        } else {
          const startTime = data.startTime[1] ? data.startTime[1] : data.startTime[0]
          const endTime = data.endTime[0] ? data.endTime[0] : data.endTime[1]
          if (!dayjs(endTime).isBefore(dayjs(startTime))) {
            cb()
          } else {
            cb(new Error('开始时间必须小于结束时间！'))
          }
        }
      }
    }

    const validateEndTime = (rule, value, cb) => {
      if (value[0] || value[1]) {
        if (!data.startTime[0] && !data.startTime[1]) {
          cb()
        } else {
          const startTime = data.startTime[1] ? data.startTime[1] : data.startTime[0]
          const endTime = data.endTime[0] ? data.endTime[0] : data.endTime[1]
          if (!dayjs(endTime).isBefore(dayjs(startTime))) {
            cb()
          } else {
            cb(new Error('结束时间必须大于开始时间！'))
          }
        }
      }
    }

    const rules = {
      startTime: [
        { validator: validateTime, trigger: 'blur' },
        { validator: validateStartTime, trigger: 'blur' }
      ],
      endTime: [
        { validator: validateTime, trigger: 'blur' },
        { validator: validateEndTime, trigger: 'blur' }
      ],
      name: [
        { required: true, trigger: 'change', message: '请输入名称' }
      ],
      taskType: [
        { required: true, trigger: 'change', message: '请输入类型' }
      ]
    }

    return {
      data,
      show,
      startTimeOrder,
      endTimeOrder,
      showTimeOrder,
      onStartTimeChange,
      onEndTimeChange,
      add,
      rules
    }
  }
}
</script>

<style lang="less" scoped>
.el-form{

  .el-form-item{
    display: flex;

    /deep/label{
      width: 7em;
    }

    /deep/.el-form-item__content{
      >*{
        margin-right: 0.5em;
      }
    }
  }
}

/deep/ .el-date-editor.el-input{
  width: 8em;
}

.input-expected-time{
  width: 9em;
}
</style>
