import { computed } from 'vue'
import { ORDER } from '@/interface/enum'
/*
* 父子组件双向绑定
* 注：只适用于父子组件、多层嵌套组件没有效果
*/
export function VModel (props, ctx) {
  return MultiVModel(props, ctx, 'modelValue')
}

export function MultiVModel (props, ctx, prop) {
  return computed({
    get () {
      return props[prop]
    },
    set (val) {
      ctx.emit(`update:${prop}`, val)
    }
  })
}

export function getOrderOptions () {
  return [{
    value: ORDER.BEFORE,
    label: '之前'
  }, {
    value: ORDER.AFTER,
    label: '之后'
  }]
}

export function parsePlanId2Number (id) {
  return Number(/[0-9]+/.exec(id)[0])
}
