<template>
  <div id="analyse">
    <Scatter
      :data="state.data"
      id="scatter"
    />

    <div>
      <div id="rects">
        <Rect color="#F24235">
          <template #data>
            {{ state.data.length }}
          </template>
          <template #description>
            任务数量
          </template>
        </Rect>
        <Rect color="#CC00FF">
          <template #data>
            {{ getFinishRate() }}
          </template>
          <template #description>
            完成率
          </template>
        </Rect>
      </div>
      <div id="bars">
        <Bar
          :data="state.data"
          title="投入度"
        />
        <Bar
          :data="state.data"
          title="满意度"
        />
      </div>
    </div>

    <div id="pies">
      <Pie
        :data="pieData"
        title="任务时长"
      />
      <Pie
        :data="pieData"
        title="任务数量"
      />
    </div>
  </div>
</template>

<script>
import { defineAsyncComponent, ref, watch } from 'vue'
import { useStore } from 'vuex'
import Scatter from './Scatter'

export default {
  name: 'Analyse',
  components: {
    Pie: defineAsyncComponent(() => import('./Pie')),
    Scatter,
    Bar: defineAsyncComponent(() => import('./Bar')),
    Rect: defineAsyncComponent(() => import('./Rect'))
  },
  setup () {
    const store = useStore()
    const state = store.state.statistics
    const taskType = store.state.taskType.data

    const pieData = ref(taskType.children.slice())

    function completeOuts (chartData) {
      chartData.forEach(e => {
        e.countValue = 0
        e.timeValue = 0
        e.taskType = e.id
        completeOuts(e.children)
      })
    }

    function transform (outs) {
      completeOuts(outs)
      state.data.forEach(e => {
        transformItem(outs, e.taskType, e.time)
      })
    }

    function transformItem (outs, types, time) {
      outs.forEach(element => {
        types.forEach(type => {
          if (element.id === type) {
            element.countValue++
            element.timeValue += time
            return null
          }
        })

        transformItem(element.children, types, time)
      })
    }

    transform(pieData.value)

    watch(state.data, () => {
      const _pieData = taskType.children.slice()
      transform(_pieData)
      pieData.value = _pieData
    })

    const getFinishRate = () => {
      const total = state.data.length
      let finish = 0
      state.data.forEach(item => {
        console.log(item)
        if (item.expectedTime && item.time >= item.expectedTime) {
          finish++
        }
      })
      return (finish / total * 100).toFixed(1) + '%'
    }

    return {
      state,
      pieData,
      getFinishRate
    }
  }
}
</script>

<style lang="less" scoped>
@s: e('/');
#analyse {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template: 50% 50% @s 70% 30%;

  >:nth-child(2){
    grid-area: 1 @s 2 @s 3 @s 3;
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;

    #bars{
      display: flex;
      flex-direction: column;
      height: 100%;
      width: 100%;

      >*{
       height: 100%;
        width: 100%;
      }
    }
  }

  >:nth-child(3){
    display: flex;
  }

  #pies {
    display: flex;
    width: 100%;
    height: 100%;
  }

  #rects {
    display: flex;

    > * {
      width: 9em;
      margin-right: 0.5em;
    }
  }

  #scatter {

  }
}
</style>
