<template>
  <div id="statistics">
    <div id="nav">
      <CurrentFilter />
      <FilterTemplate />
      <el-radio-group v-model="state.mode">
        <el-radio-button label="data">
          数据
        </el-radio-button>
        <el-radio-button label="analyse">
          分析
        </el-radio-button>
      </el-radio-group>
    </div>
    <div id="chart">
      <Data v-if="state.mode==='data'" />
      <Analyse v-else />
    </div>
  </div>
</template>

<script>
import { ref } from 'vue'
import { useStore } from 'vuex'
import CurrentFilter from './CurrentFilter/Index'
import FilterTemplate from './FilterTemplate/Index'
import Data from './Data/Index'
import Analyse from './Analyse/Index'

export default {
  name: 'Statistics',
  components: {
    CurrentFilter,
    FilterTemplate,
    Data,
    Analyse
  },
  setup () {
    const store = useStore()
    store.dispatch('statistics/initFilter')
    store.dispatch('statistics/getDataWithId', 4)

    const state = store.state.statistics

    const chartTypes = [{
      value: 'list',
      label: '列表'
    }, {
      value: 'pie',
      label: '扇形图'
    }, {
      value: 'scatter',
      label: '散点图'
    }]

    const activeTab = ref(0)
    const onTabClick = () => {

    }

    const currentChartType = ref('list')

    return {
      state,
      activeTab,
      onTabClick,
      chartTypes,
      currentChartType
    }
  }
}
</script>

<style lang="less" scoped>
@s:e('/');

#statistics{
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;

  #nav{
    display: flex;
    align-items: center;

    .el-radio-group{
      margin-left: auto;
    }
  }

  #chart{
    width: 100%;
    height: 100%;
    margin-top: 1em;
  }
}
</style>
